import React from 'react'
import { Control, Controller } from 'react-hook-form'
import { FormHelperText, Typography, IconButton, Box } from '@mui/material'
import useStyles from '../styles'
import ResponseSearchBar from '../../Searchers/ResponseSearchBar/ResponseSearchBar'
import IconDeleteButton from '../../../../assets/Icons/IconDeleteButton/IconDeleteButton'

interface ControlledResponseFieldProps {
  control: Control
  error: boolean
  helperText: string
  width: number
  hideLabel: boolean
  onResponseChange: (value: string) => void
  responseValue: string
  handleDelete: () => void
}

const ControlledResponseField = ({
  control,
  error,
  helperText,
  width,
  hideLabel,
  onResponseChange,
  responseValue,
  handleDelete,
}: ControlledResponseFieldProps) => {
  const classes = useStyles()

  return (
    <div className={classes.fieldContainer} style={{ width }}>
      {!hideLabel && <Typography variant="body2">Компания</Typography>}
      <Box sx={{ display: 'flex', alignItems: 'center' }}>
        <Controller
          name="responses_field"
          control={control}
          render={({ field }) => (
            <ResponseSearchBar
              value={field?.value}
              onChange={field?.onChange}
              error={error}
              responseValue={responseValue}
              onResponseChange={onResponseChange}
            />
          )}
        />
        <IconButton sx={{ mb: '-1px' }} onClick={handleDelete}>
          <IconDeleteButton />
        </IconButton>
      </Box>

      <div className={classes.input} style={{ marginTop: -4 }}>
        <FormHelperText className="Mui-error">{helperText || ' '}</FormHelperText>
      </div>
    </div>
  )
}

export default React.memo(ControlledResponseField)
