import React, { FC, useState } from 'react'
import { FormControl, FormHelperText, InputLabel, MenuItem, Select, Typography } from '@material-ui/core'
import { MenuProps } from '@mui/material'
import { IconExpandMore } from '../../../../assets/Icons'
import useStyles from './controlled-select-field.styles'
import { Cohort, Course } from '../../../../services/types/onboarding/onboarding-types'

type Props = {
  labelTitle?: string
  tipContent?: string
  tipBlack?: string
  // value: string | { [n: string]: string | number | undefined } | number
  value: any
  error?: boolean
  helperText?: string
  onChange: (arg0: any) => void
  // items: Array<{ [n: string]: string | number | undefined }> | Course[] | Cohort[]
  items: any
  maxWidth?: number
  disabled?: boolean
  selectAll?: boolean
  height?: string | number
  marginLeft?: string | number
  marginRight?: string | number
  marginTop?: string | number
  marginBottom?: string | number
  isColor?: boolean
  className?: string
  multiple?: boolean
}

const iconComponent = (props: Props) => <IconExpandMore className={`${props.className}`} />

const ControlledSelectField: FC<Props> = ({
  labelTitle,
  tipContent,
  tipBlack,
  value,
  error,
  helperText,
  onChange,
  items,
  maxWidth,
  disabled,
  selectAll,
  height,
  isColor,
  marginLeft,
  marginRight,
  marginTop,
  marginBottom,
  ...props
}) => {
  // TODO слишком много разных значений может скушать компонент
  const [multipleValues, setMultipleValues] = useState((value as any) || [])
  const classes = useStyles()

  const selectMenuProps = {
    classes: {
      paper: classes.paper,
      list: classes.list,
    },
    anchorOrigin: {
      vertical: 'bottom',
      horizontal: 'left',
    },
    transformOrigin: {
      vertical: 'top',
      horizontal: 'left',
    },
    autoFocus: false,
    getContentAnchorEl: null,
    disableScrollLock: true,
  }

  const isAllSelected = props.multiple && items.length > 0 && multipleValues.length === items.length

  const renderValue = (values: any) => {
    const color = tipBlack ? '#1A1B22' : '#797981'
    if (!values || values?.length === 0) {
      return <span style={{ color }}>{tipContent}</span>
    }
    const selected = items?.filter((option: any) => values?.includes(option?.id))?.map((option: any) => option?.name)
    return selected.join(', ')
  }

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedValues = e?.target?.value

    if (selectedValues.includes('all')) {
      const valueToSet = multipleValues.length === items.length ? [] : items?.map((o: any) => o?.id)
      setMultipleValues(valueToSet as any)
      onChange({ ...e, target: { ...e.target, value: valueToSet } })
      return
    }

    setMultipleValues(selectedValues)
    onChange({ ...e, target: { ...e.target, value: selectedValues } })
  }

  return (
    <div
      className={labelTitle ? classes.fieldContainer : ''}
      style={{ maxWidth, minWidth: maxWidth, height, marginLeft, marginRight, marginTop, marginBottom }}
    >
      {labelTitle && <Typography variant="body2">{labelTitle}</Typography>}
      <FormControl
        hiddenLabel
        className={`${classes.input} ${classes.inputSelect}`}
        variant="outlined"
        error={error && (!value || value?.length === 0)}
        disabled={disabled}
      >
        {!value && (
          <InputLabel variant="filled" shrink={false} className={classes.labelSelect} error={false}>
            {tipContent}
          </InputLabel>
        )}
        <Select
          MenuProps={selectMenuProps as any}
          IconComponent={iconComponent}
          onChange={selectAll ? (handleChange as any) : onChange}
          value={selectAll ? multipleValues : value}
          style={{ maxWidth, minWidth: maxWidth }}
          disabled={disabled}
          renderValue={props?.multiple && (renderValue as any)}
          {...props}
          autoFocus={false}
        >
          {selectAll && (
            <MenuItem value="all" className={isAllSelected ? 'Mui-selected' : undefined}>
              <span>{selectAll}</span>
            </MenuItem>
          )}
          {items?.map((item: any) => (
            <MenuItem
              key={item.id || 0}
              value={item.id}
              style={{ minWidth: maxWidth ? maxWidth - 5 : undefined, maxWidth: maxWidth ? maxWidth - 5 : undefined }}
            >
              {/* eslint-disable-next-line no-nested-ternary */}
              {item.icon ? (
                <div className={classes.selectContainer}>
                  <img src={item.icon as string} alt={item.name as string} />
                  <span className={classes.selectName}>{item.name}</span>
                </div>
              ) : isColor ? (
                <span className={classes.selectName}>{item.name}</span>
              ) : (
                <span dangerouslySetInnerHTML={{ __html: item.name as string }} />
              )}
            </MenuItem>
          ))}
        </Select>
        <FormHelperText>{helperText || ' '}</FormHelperText>
      </FormControl>
    </div>
  )
}

export default React.memo(ControlledSelectField)
