import React from 'react'
import { differenceInDays, isToday } from 'date-fns'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import { Typography, TextField, FormHelperText, IconButton, InputAdornment } from '@material-ui/core'
import { useNavigate } from 'react-router-dom'

import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import DatePicker from '../../CustomDatePicker/CustomDatePicker'
import { IconClose, IconAttention, IconCalendar } from '../../../../assets/Icons'

import useStyles from './VacationActionPopup.style'
import api from '../../../../api/api'
import useAsync from '../../../../hooks/useAsync'
import { newVacationSchema } from '../../../../utils/schemas'
import {
  API_HOLIDAYS_ENDPOINT,
  API_HOLIDAYS_COUNTER_ENDPOINT,
  EXIT_TIMER,
  API_SET_HOLIDAYS_ENDPOINT,
} from '../../../../utils/constants'
import { formatApiDate, formatRusStyle } from '../../../../utils/dates'
import processServerError from '../../../../utils/server-error'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { signOut } from '../../../../services/axios/auth'
import { clearTimeoutId, setVacation } from '../../../../services/slices/auth-slice'

// eslint-disable-next-line camelcase
const VacationActionPopup = ({ openState = [], setSnack, studentToEdit = undefined, setSuccessPatch = undefined }) => {
  const dispatch = useAppDispatch()
  const [isOpen, setIsOpen] = openState
  const navigate = useNavigate()
  const [startDate, setStartDate] = React.useState(new Date())
  const [endDate, setEndDate] = React.useState(null)
  const [holidays, setHolidays] = React.useState(0)
  const { timeoutId } = useAppSelector((store) => store.auth)
  const { run } = useAsync({
    data: null,
    error: null,
  })

  const {
    handleSubmit,
    setValue,
    setError,
    reset,
    formState: { errors },
  } = useForm({ resolver: yupResolver(newVacationSchema), mode: 'onChange' })

  const handleCloseExit = async () => {
    try {
      // Останавливаем запрос статуса
      clearTimeout(timeoutId)
      dispatch(clearTimeoutId())
      await dispatch(signOut())
      await dispatch(setVacation(endDate))
      navigate('/sign-in')
    } catch (e) {
      return processServerError(e, setError)
    }
    return 0
  }

  const classes = useStyles()

  const handleClose = () => {
    setIsOpen(false)
    reset()
    // Визуально календарь не очищается через reset, очищаем вручную
    setEndDate(null)
    setStartDate(new Date())
    setValue('start_date', startDate, {
      shouldValidate: true,
      shouldDirty: true,
    })
  }

  const onCreateHoliday = async (data) => {
    if (!studentToEdit) {
      try {
        const response = await api.post(API_HOLIDAYS_ENDPOINT, {
          ...data,
          start_date: formatApiDate(startDate),
          end_date: formatApiDate(endDate),
        })
        if (response.status === 201) {
          setSnack({
            open: true,
            header: 'Хорошего отпуска!',
            message: isToday(startDate)
              ? 'Сейчас выполнится автоматический выход'
              : `Автоматический выход выполнится ${formatRusStyle(startDate)}`,
          })
          handleClose()
          // Выйдем из аккаунта, если дата отпуска начинается с сегодня
          if (isToday(startDate)) {
            setTimeout(handleCloseExit, EXIT_TIMER)
          }
        }
        return Promise.resolve(response.data)
      } catch (e) {
        return processServerError(e, setError)
      }
    } else {
      try {
        const response = await api.post(API_SET_HOLIDAYS_ENDPOINT, {
          ...data,
          start_date: formatApiDate(startDate),
          end_date: formatApiDate(endDate),
          profile_id: studentToEdit.id,
        })
        if (response.status === 201) {
          setSnack({
            open: true,
            header: 'Отпуск создан',
            message: isToday(startDate)
              ? 'Сейчас у студента выполнится автоматический выход'
              : `Автоматический выход у студента выполнится ${formatRusStyle(startDate)}`,
          })
          setSuccessPatch(true)
          handleClose()
        }
        return Promise.resolve(response.data)
      } catch (e) {
        return processServerError(e, setError)
      }
    }
  }

  const onSubmit = (data) => {
    if (holidays !== 0) {
      const holidaysCount = differenceInDays(endDate, startDate) + 2
      if (holidaysCount <= holidays) {
        setHolidays(holidays - holidaysCount)
        run(onCreateHoliday(data))
      } else if (holidays >= 7) {
        setError('end_date', { message: `Сократи срок. У тебя осталось ${holidays} дней отпуска` })
      } else {
        setError('end_date', { message: `Ты больше не можешь выходить в отпуск` })
      }
    } else {
      setError('end_date', { message: `Ты больше не можешь выходить в отпуск` })
    }
  }

  const getHolidaysSpent = async () => {
    try {
      const response = await api.get(API_HOLIDAYS_COUNTER_ENDPOINT)
      setHolidays(60 - response.data.holidays_count)
      return Promise.resolve(response.data)
    } catch (e) {
      return Promise.reject(e.response)
    }
  }

  React.useEffect(() => {
    setValue('start_date', startDate, {
      shouldValidate: true,
      shouldDirty: true,
    })
    if (!studentToEdit) {
      run(getHolidaysSpent())
    }
  }, [])

  React.useEffect(() => {
    if (studentToEdit?.id) {
      setHolidays(60 - studentToEdit.holidays_count)
    }
  }, [studentToEdit])

  return (
    <div>
      <Dialog classes={{ paper: classes.root }} open={isOpen} onClose={handleClose} aria-labelledby="form-dialog-title">
        <div className={classes.container}>
          <IconButton onClick={handleClose} className={classes.closePopup}>
            <IconClose />
          </IconButton>

          <DialogContent className={classes.dialogContent}>
            <form className={classes.form} onSubmit={handleSubmit(onSubmit)} id="form-vacations">
              <div className={classes.topContainer}>
                <div className={classes.titleContainer}>
                  <Typography variant="h2" id="form-dialog-title" className={classes.title}>
                    Отпуск
                  </Typography>
                  <Typography variant="body1" color="textSecondary">
                    {studentToEdit
                      ? `Ты можешь установить студенту отпуск  до 60 календарных дней за все время. (Осталось дней: ${holidays})`
                      : `Ты можешь взять до 60 календарных дней за все время. (Осталось дней: ${holidays})`}
                  </Typography>
                </div>
              </div>
              <div className={classes.fields}>
                {studentToEdit && (
                  <div className={classes.fieldContainer}>
                    <Typography>Студент</Typography>
                    <TextField
                      className={classes.input}
                      variant="outlined"
                      value={`${studentToEdit?.first_name} ${studentToEdit?.last_name}`}
                      InputProps={{ disableUnderline: 'true' }}
                      disabled
                    />
                  </div>
                )}
                <div className={classes.fieldContainer}>
                  <Typography>Дата начала отпуска</Typography>
                  <DatePicker
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="end">
                          <IconButton>
                            <IconCalendar isGray={!startDate} />
                          </IconButton>
                        </InputAdornment>
                      ),
                    }}
                    cancelLabel="Отменить"
                    okLabel="Сохранить"
                    className={classes.picker}
                    inputVariant="outlined"
                    format="dd MMM yyyy"
                    disableToolbar
                    variant="dialog"
                    error={errors.start_date?.message}
                    helperText={errors.start_date?.message}
                    value={startDate}
                    onChange={(val) => {
                      setValue('start_date', val, {
                        shouldValidate: true,
                        shouldDirty: true,
                      })
                      setStartDate(val)
                    }}
                  />
                </div>
                <div className={classes.fieldContainer}>
                  <Typography>Дата окончания отпуска</Typography>
                  <DatePicker
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="end">
                          <IconButton>
                            <IconCalendar isGray={!endDate} />
                          </IconButton>
                        </InputAdornment>
                      ),
                    }}
                    cancelLabel="Отменить"
                    okLabel="Сохранить"
                    inputVariant="outlined"
                    format="dd MMM yyyy"
                    disableToolbar
                    variant="dialog"
                    value={endDate}
                    error={errors.end_date?.message}
                    helperText={errors.end_date?.message}
                    onChange={(val) => {
                      setValue('end_date', val, {
                        shouldValidate: true,
                        shouldDirty: true,
                      })
                      setEndDate(val)
                    }}
                  />
                </div>
              </div>
              <div className={classes.info}>
                <IconAttention />
                <span>
                  <span className={classes.caption1}>С Даты начала отпуска </span>
                  <span className={classes.caption2}>
                    {studentToEdit
                      ? `выполнится выход из аккаунта, и студент не сможет в него зайти до окончания отпуска`
                      : `выполнится выход из аккаунта и ты не сможешь в него зайти до окончания отпуска`}
                  </span>
                </span>
              </div>

              <FormHelperText error style={{ textAlign: 'center', margin: '5px 0' }}>
                {errors.non_field_errors?.message || ' '}
              </FormHelperText>
            </form>
            <Button form="form-vacations" className={classes.submitButton} type="submit">
              {studentToEdit ? 'Начать отпуск' : 'Отправить'}
            </Button>
          </DialogContent>
        </div>
      </Dialog>
    </div>
  )
}

export default React.memo(VacationActionPopup)
