/* eslint-disable no-shadow */
/* eslint-disable no-return-assign */
import * as React from 'react'
import { signOut } from '../services/axios/auth'
import { useAppDispatch } from '../services/store/hooks'
import showAchievementsNotifications from '../utils/showAchievementsNotifications'

function useSafeDispatch(dispatch) {
  const mounted = React.useRef(false)
  React.useLayoutEffect(() => {
    mounted.current = true
    return () => (mounted.current = false)
  }, [])
  return React.useCallback(
    // eslint-disable-next-line no-void
    (...args) => (mounted.current ? dispatch(...args) : void 0),
    [dispatch],
  )
}

const defaultInitialState = { status: 'idle', data: null, error: null }
function useAsync(initialState) {
  const dispatch = useAppDispatch()
  const initialStateRef = React.useRef({
    ...defaultInitialState,
    ...initialState,
  })
  const [{ status, data, error }, setState] = React.useReducer((s, a) => ({ ...s, ...a }), initialStateRef.current)

  const safeSetState = useSafeDispatch(setState)

  React.useEffect(() => {
    if (
      (error &&
        (error === 'Request failed with status code 401' ||
          error?.status === 403 ||
          error?.data?.code === 'token_not_valid')) ||
      (error?.data?.refresh && error?.data?.refresh.join('') === 'Обязательное поле.')
    ) {
      dispatch(signOut())
    }
  }, [error])

  const run = React.useCallback(
    (promise) => {
      if (!promise || !promise.then) {
        throw new Error(
          `The argument passed to useAsync().run must be a promise. Maybe a function that's passed isn't returning anything?`,
        )
      }
      safeSetState({ status: 'pending' })
      return promise.then(
        (data) => {
          safeSetState({ data, status: 'resolved' })
          const achievements = data?.achievements || data?.data?.achievements
          if (achievements) {
            showAchievementsNotifications(achievements, dispatch)
          }
          return data
        },
        (error) => {
          safeSetState({ status: 'rejected', error })
          return error
        },
      )
    },
    [safeSetState],
  )

  const setData = React.useCallback((data) => safeSetState({ data }), [safeSetState])
  const setError = React.useCallback((error) => safeSetState({ error }), [safeSetState])
  const reset = React.useCallback(() => safeSetState(initialStateRef.current), [safeSetState])

  return {
    isIdle: status === 'idle',
    isLoading: status === 'pending',
    isError: status === 'rejected',
    isSuccess: status === 'resolved',

    setData,
    setError,
    error,
    status,
    data,
    run,
    reset,
  }
}

export default useAsync
