/* eslint-disable no-unused-vars */
import React, { useEffect, useState } from 'react'
import { Avatar, Toolbar } from '@material-ui/core'
import useStyles from './ToolPanel.style'
import ToolButton from '../Buttons/ToolButton/ToolButton'
import noAvatar from '../../../assets/nopic.jpg'
import FoundJobPopup from '../GlobalPopups/FoundJobPopup/FoundJobPopup'
import { NewReportPopup } from '../GlobalPopups'

const ToolPanel = ({ profile, setSnack, refreshFeeds }) => {
  const [isJobModalOpen, setIsJobModalOpen] = useState(false)
  const [isReportModalOpen, setIsReportModalOpen] = useState(false)
  const classes = useStyles()
  const profileInfo = profile ? (
    <article className={classes.userInfo}>
      <Avatar src={profile.image ? profile.image : noAvatar} alt="Аватар пользователя" className={classes.avatar} />
      <div>
        <h1 className={classes.header}>
          {profile.first_name} {profile.last_name}
        </h1>
        <p className={classes.legend}>
          {!!profile.course && <span className={classes.text}>{profile.course.name}</span>}
          {!!profile.cohort_number && <span className={classes.text}>Поток {profile.cohort_number}</span>}
          {!!profile.city && <span className={classes.text}>{profile.city}</span>}
        </p>
      </div>
    </article>
  ) : null

  useEffect(() => {
    if (profile?.last_report_update_datetime) {
      const lastReportDate = new Date(profile.last_report_update_datetime)
      const now = new Date()
      const isReportOlderOneWeek = (now - lastReportDate) / (24 * 3600 * 1000 * 7) > 1
      const storageDate = localStorage.getItem('lastPopupDate')
      const lastReportPopupDate = storageDate ? new Date(storageDate) : lastReportDate
      const isPopupDateOlderOneDay = (now - lastReportPopupDate) / (24 * 3600 * 1000) > 1
      if (isReportOlderOneWeek && isPopupDateOlderOneDay) {
        localStorage.setItem('lastPopupDate', now)
        setIsReportModalOpen(true)
      }
    }
  }, [])

  return (
    <>
      <FoundJobPopup openState={[isJobModalOpen, setIsJobModalOpen]} setSnack={setSnack} />
      <NewReportPopup
        open={isReportModalOpen}
        setOpen={setIsReportModalOpen}
        setSnack={setSnack}
        refreshFeeds={refreshFeeds}
        openFoundJobPopup={() => setIsJobModalOpen(true)}
      />
      <Toolbar className={classes.root}>
        {profile ? profileInfo : <div />}
        <div className={classes.buttonGroup}>
          <ToolButton onClick={() => setIsReportModalOpen(true)}>Создать запись</ToolButton>
        </div>
      </Toolbar>
    </>
  )
}

export default ToolPanel
