import React, { FC, useEffect, useMemo } from 'react'
import debounce from 'lodash.debounce'
import TextField from '@mui/material/TextField'
import InputAdornment from '@mui/material/InputAdornment'
import Autocomplete from '@mui/material/Autocomplete'
import CircularProgress from '@mui/material/CircularProgress'
import { AxiosError } from 'axios'
import useStyles from './CompanySearchBar.style'
import api from '../../../../api/api'
import useAsync from '../../../../hooks/useAsync'
import { API_COMPANY_SEARCH_ENDPOINT } from '../../../../utils/constants'
import IconSearch from '../../../../assets/Icons/IconSearch/IconSearch'

type TCompany = {
  id: number
  name: string
}

type Props = {
  onChange: (...event: any[]) => void
  value: TCompany
  error?: boolean
}

// type TDebounceHandler = (event: SyntheticEvent<Element, Event>, value: string, reason: AutocompleteInputChangeReason) => void

const CompanySearchBar: FC<Props> = ({ onChange, value, error }) => {
  const { run } = useAsync({
    data: [],
    error: null,
  })
  const [open, setOpen] = React.useState(false)
  const [inputValue, setInputValue] = React.useState<string | null>(null)
  const [options, setOptions] = React.useState<Array<TCompany>>([])
  const [loading, setLoading] = React.useState(false)
  const classes = useStyles()

  const getCompanies = async (query: string) => {
    try {
      setLoading(true)
      const response = await api.get(API_COMPANY_SEARCH_ENDPOINT, { params: { search: query } })
      const companies = response?.data
      // setOptions(companies && companies.map((c: TCompany) => `${c?.name}`))
      setOptions(companies)
      setOpen(companies?.length > 0)

      return Promise.resolve(response?.data)
    } catch (e) {
      const err = e as AxiosError
      return Promise.reject(err.response)
    } finally {
      setLoading(false)
    }
  }

  const changeHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event?.target?.value) {
      setInputValue(event?.target?.value)
      onChange(event?.target?.value)
    }
  }

  const debouncedChangeHandler = useMemo(() => debounce(changeHandler, 300), [])

  useEffect(() => () => debouncedChangeHandler.cancel(), [])

  useEffect(() => {
    if (inputValue && inputValue?.length > 2 && inputValue !== value?.name) {
      run(getCompanies(inputValue))
    }
  }, [inputValue])

  useEffect(() => {
    if (!open) {
      setOptions([])
    }
  }, [open])

  return (
    <Autocomplete
      sx={{ width: 400, height: 40 }}
      clearText="Очистить"
      loadingText="Загрузка..."
      noOptionsText=""
      autoHighlight
      freeSolo
      open={open}
      className={classes.root}
      classes={{ paper: classes.paper }}
      onOpen={() => {
        setOpen(options.length > 0)
      }}
      onClose={() => {
        setOpen(false)
      }}
      isOptionEqualToValue={(option) => option === value}
      /* eslint-disable-next-line @typescript-eslint/ban-ts-comment */
      // @ts-ignore
      getOptionLabel={(option) => (typeof option === 'object' ? option.name : option)}
      filterOptions={(x) => x}
      options={options}
      loading={loading}
      value={value}
      // Без этого будет несовпадение с типом ебаунсера
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      onInputChange={debouncedChangeHandler}
      onChange={(event, item) => {
        onChange(item || undefined)
      }}
      renderInput={(params) => (
        <TextField
          {...params}
          error={error}
          placeholder="Введи название компании"
          InputProps={{
            ...params.InputProps,
            startAdornment: (
              <InputAdornment position="start">
                <IconSearch />
              </InputAdornment>
            ),
            endAdornment: (
              <>
                {loading ? <CircularProgress color="inherit" size={20} /> : null}
                {params.InputProps.endAdornment}
              </>
            ),
          }}
        />
      )}
    />
  )
}

export default React.memo(CompanySearchBar)
