import { Button } from '@material-ui/core'
import React from 'react'
import useStyles from './ToolButton.style'

const ToolButton = ({ children, typeSecondary, typeBig, ...props }) => {
  const classes = useStyles()
  const buttonClass = `${classes.root} ${typeSecondary && classes.secondary} ${typeBig && classes.big}`

  return (
    <Button {...props} disableElevation className={buttonClass}>
      {children}
    </Button>
  )
}

export default ToolButton
