import React, { useEffect } from 'react'
import { Box, Dialog, DialogContent, Typography, ThemeProvider, Button, DialogActions } from '@mui/material'
import { Controller, useForm } from 'react-hook-form'
import { parseISO } from 'date-fns'
import { yupResolver } from '@hookform/resolvers/yup'
import { useAppDispatch, useAppSelector } from '../../../../../services/store/hooks'
import theme from '../../../../../utils/themeMUI5'
import { ControlledDateField } from '../../../../Sub/FormElements'
import { changeStatusSchema } from '../../../../../utils/schemas'
import { cancelDragging, finishDragging } from '../../../../../services/slices/diary-slice'
import { TEMP_STATUSES } from '../../../../../utils/constants'
import { patchResponse } from '../../../../../services/axios/diary'
import { formatApiDate } from '../../../../../utils/dates'
import { enqueueSnackbar } from '../../../../../services/slices/app-slice'

const NewStatusPopup = () => {
  const dispatch = useAppDispatch()
  const { draggingStatus } = useAppSelector((state) => state.diary)

  const open = draggingStatus.stage === 'dragging'

  const currentStatus = TEMP_STATUSES.find((status) => status.status_id === draggingStatus.newStatus)

  const today = new Date()

  const { control, handleSubmit, setValue } = useForm({
    defaultValues: {
      date: today,
      response_date: today,
    },
    resolver: yupResolver(changeStatusSchema),
    mode: 'onChange',
  })

  useEffect(() => {
    if (draggingStatus.responseDate) {
      setValue('response_date', parseISO(draggingStatus.responseDate))
    }
  }, [draggingStatus.responseDate])

  const handleCancelClick = () => {
    dispatch(cancelDragging())
  }

  const onSubmit = async (data: { date: Date; response_date: Date }) => {
    try {
      if (currentStatus?.status_id === undefined || draggingStatus.responseId == null)
        throw new Error("Can't find status")
      const dateFieldName = `${currentStatus.status_id}_date`
      const result = await dispatch(
        patchResponse({
          id: +draggingStatus.responseId,
          data: {
            [dateFieldName]: formatApiDate(data.date),
            last_update_datetime: formatApiDate(new Date()),
          },
        }),
      )
      dispatch(
        enqueueSnackbar({
          info: {
            type: 'common',
            title: 'Отклик обновлён',
            message: 'Данные успешно сохранены',
          },
          options: {
            key: new Date().getTime() + Math.random(),
            autoHideDuration: 6000,
          },
        }),
      )
      dispatch(finishDragging())
      return Promise.resolve(result)
    } catch (e) {
      return Promise.reject(e)
    }
  }

  return (
    <ThemeProvider theme={theme}>
      <Dialog
        open={open}
        onClose={(_, reason) => {
          if (reason !== 'backdropClick' && reason !== 'escapeKeyDown') {
            handleCancelClick()
          }
        }}
        sx={{
          '& .MuiPaper-root': {
            width: 386,
            maxWidth: 386,
            height: 318,
            maxHeight: 318,
            overflow: 'hidden',
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'space-between',
          },
        }}
      >
        <DialogContent sx={{ '&.MuiDialogContent-root': { paddingTop: 0, color: '#1A1B22' } }}>
          <Box
            sx={{
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'start',
              width: '100%',
              marginBottom: '32px',
            }}
          >
            <Typography variant="h2">Смена этапа</Typography>
            <Typography variant="body3" sx={{ color: '#797981', fontWeight: 400, marginTop: '4px' }}>
              Убедись, что этап и дата выбраны верно
            </Typography>
          </Box>
          <Controller
            name="date"
            control={control}
            render={({ field: { onChange, value }, fieldState: { error } }) => (
              <ControlledDateField
                labelTitle={currentStatus?.title}
                fieldName="date"
                dateValue={value}
                error={!!error}
                helperText={error?.message}
                onChange={onChange}
                type="standard"
              />
            )}
          />
        </DialogContent>
        <DialogActions
          sx={{
            '&.MuiDialogActions-root': { display: 'flex', flexDirection: 'row', justifyContent: 'space-between' },
          }}
        >
          <Button
            type="button"
            color="secondary"
            sx={{ width: 143, minHeight: 50, fontSize: 16 }}
            onClick={handleCancelClick}
          >
            Отменить
          </Button>
          <Button
            type="button"
            color="primary"
            sx={{ width: 143, minHeight: 50, fontSize: 16 }}
            onClick={handleSubmit(onSubmit)}
          >
            Сменить
          </Button>
        </DialogActions>
      </Dialog>
    </ThemeProvider>
  )
}

export default React.memo(NewStatusPopup)
