export const newResponseSchema = yup.object().shape({
  company_name: yup
    .string()
    .required('Введи название компании')
    .min(RESPONSE_TEXT_FIELDS_MIN_SYMBOLS, RESPONSE_TEXT_FIELD_MINMAX_TIP)
    .max(RESPONSE_TEXT_FIELD_MAX_SYMBOLS, RESPONSE_TEXT_FIELD_MINMAX_TIP)
    .matches(RESPONSE_WORK_REGEXP, RESPONSE_WORK_TIP),
  position: yup
    .string()
    .required('Введи должность')
    .min(RESPONSE_TEXT_FIELDS_MIN_SYMBOLS, RESPONSE_TEXT_FIELD_MINMAX_TIP)
    .max(RESPONSE_TEXT_FIELD_MAX_SYMBOLS, RESPONSE_TEXT_FIELD_MINMAX_TIP)
    .matches(RESPONSE_WORK_REGEXP, RESPONSE_WORK_TIP),
  is_remote_work: yup.boolean(),
  cities: yup.array().nullable(true),
  comment: yup
    .string('Комментарий должен быть текстом')
    .max(RESPONSE_COMMENT_MAX_SYMBOLS, RESPONSE_COMMENT_MAX_SYMBOLS_TIP)
    .nullable(true),
  url: urlOption,
  salary: yup
    .number()
    .max(1000000000, 'Вряд ли это число может быть больше 1000000000')
    .nullable(true)
    .moreThan(-1, 'Зарплата не может быть меньше нуля')
    .typeError('Введи число')
    .transform((value, original) => (original == null || original === '' ? null : value)),
  salary_max: yup
    .number()
    .max(1000000000, 'Вряд ли это число может быть больше 1000000000')
    .nullable(true)
    .moreThan(-1, 'Зарплата не может быть меньше нуля')
    .typeError('Введи число')
    .transform((value, original) => (original == null || original === '' ? null : value))
    .when('salary', {
      is: (val) => val,
      then: (theme) => theme.min(yup.ref('salary'), 'Должно быть больше значения "до"'),
    }),
  color_id: yup.string().required('Выберите цвет'),
  response_date: yup
    .date()
    .required('Обязательное поле')
    .test('max-current-date', 'Дата отклика не может быть больше текущей даты', maxDateTest)
    .test('min-two-weeks', 'Нельзя вносить отклики, которые были отправлены более 14 дней назад', minResponseDate)
    .typeError('Введи дату'),
  hr_interview_date: yup
    .date()
    .nullable(true)
    .when(
      'response_date',
      (responseDate, schema) => responseDate && schema.min(responseDate, 'Не может быть ранее даты отклика'),
    )
    .typeError('Введи дату'),
  test_task_date: yup
    .date()
    .nullable(true)
    .when(
      'response_date',
      (responseDate, schema) => responseDate && schema.min(responseDate, 'Не может быть ранее даты отклика'),
    )
    .typeError('Введи дату'),
  tech_interview_date: yup
    .date()
    .nullable(true)
    .when(
      'response_date',
      (responseDate, schema) => responseDate && schema.min(responseDate, 'Не может быть ранее даты отклика'),
    )
    .typeError('Введи дату'),
  boss_interview_date: yup
    .date()
    .nullable(true)
    .when(
      'response_date',
      (responseDate, schema) => responseDate && schema.min(responseDate, 'Не может быть ранее даты отклика'),
    )
    .typeError('Введи дату'),
  offer_date: yup
    .date()
    .nullable(true)
    .when(
      'response_date',
      (responseDate, schema) => responseDate && schema.min(responseDate, 'Не может быть ранее даты отклика'),
    )
    .typeError('Введи дату'),
  rejection_date: yup
    .date()
    .nullable(true)
    .when(
      'response_date',
      (responseDate, schema) => responseDate && schema.min(responseDate, 'Не может быть ранее даты отклика'),
    )
    .typeError('Введи дату'),
})

