import React from 'react'
import { Controller } from 'react-hook-form'
import { FormHelperText, Typography } from '@mui/material/'
import { CityIdSearchBar, CourseSearchBar } from '../../Searchers'
import useStyles from '../styles'

const ControlledCourseField = ({ control, error, helperText, name, width, hideLabel = undefined }) => {
  const classes = useStyles()

  return (
    <div className={classes.fieldContainer} style={{ width }}>
      {!hideLabel && <Typography variant="body2">Курс</Typography>}
      <Controller
        name={name}
        control={control}
        render={({ field }) => <CourseSearchBar value={field?.value} onChange={field?.onChange} error={error} />}
      />
      <div className={classes.input}>
        <FormHelperText error>{helperText || ' '}</FormHelperText>
      </div>
    </div>
  )
}

export default React.memo(ControlledCourseField)
