import React from 'react'
import { InputAdornment, Typography, FormHelperText, FormControl } from '@material-ui/core'
import { ControlledTextField, ControlledSelectField } from "../../FormElements"
import Dropzone from '../../Dropzone/Dropzone'
import useStyles from './EditProfilePopup.styles'

function SecondTabProfile({ values, handlers }) {
  const { control, errors, methodValue, comMethods, addedFiles, deletedFiles, setIsFilesChanged } = values

  const { handleMethodChange } = handlers

  const classes = useStyles()

  const isTelegram = methodValue === 1
  const contactPlaceholder = isTelegram ? 'Введи свой ник' : 'Введи свой email'
  const contactClassName = isTelegram
    ? `${classes.resumeInput} ${classes.inputTelegram}`
    : `${classes.resumeInput} ${classes.inputEmail}`
  const contactAdornment = isTelegram
    ? {
        startAdornment: (
          <InputAdornment
            position="start"
            sx={{
              marginRight: 0,
              '.MuiTypography-root': {
                color: '#1a1b22',
                fontFamily: ['YS Text', '-apple-system', 'BlinkMacSystemFont', 'Arial', 'Helvetica', 'sans-serif'].join(
                  ',',
                ),
                fontSize: 14,
                lineHeight: '20px',
              },
            }}
          >
            @
          </InputAdornment>
        ),
      }
    : {}

  return (
    <fieldset className={classes.fieldsetResume}>
      <div className={classes.fieldContainerResume}>
        <Typography variant="body2">Контакты для связи с рекрутером</Typography>
        <div className={classes.twoFieldInRowContainer}>
          <ControlledSelectField
            value={methodValue}
            error={!!errors?.contact_type}
            helperText={errors?.contact_type?.message}
            onChange={handleMethodChange}
            items={comMethods}
            maxWidth={148}
          />
          <ControlledTextField
            fieldName="contact_info"
            control={control}
            error={!!errors.contact_info?.message}
            helperText={errors.contact_info?.message}
            textClass={contactClassName}
            placeholder={contactPlaceholder}
            inputProps={contactAdornment}
            maxWidth={226}
          />
        </div>
      </div>
      <div className={classes.fieldResumeContainer}>
        <Dropzone
          filesState={addedFiles}
          deletedFiles={deletedFiles}
          setIsFilesChanged={setIsFilesChanged}
          isEditPopup
        />
        <FormControl className={classes.input}>
          <FormHelperText error>{errors?.files?.message || ' '}</FormHelperText>
        </FormControl>
      </div>
      <div className={classes.fieldResumeContainer}>
        <ControlledTextField
          labelTitle="Ссылка на портфолио (необязательно)"
          fieldName="portfolio_url"
          control={control}
          error={!!errors.portfolio_url?.message}
          helperText={errors.portfolio_url?.message}
          placeholder="Введи адрес"
          textClass={classes.resumeInputLink}
          multiline
          rows={3}
        />
      </div>
    </fieldset>
  )
}

export default React.memo(SecondTabProfile)
