import React from 'react'
import {
  ELLIPSE_GREEN_COLORS,
  ELLIPSE_ORANGE_COLORS,
  ELLIPSE_ORCHID_COLORS,
  ELLIPSE_BLUE_COLORS,
} from '../../../utils/constants'

const IconEllipse = ({ isGreen, isOrange, isOrchid, isBlue }) => (
  <svg width="8" height="8" viewBox="0 0 8 8" fill="none" xmlns="http://www.w3.org/2000/svg">
    <ellipse
      cx="4"
      cy="4"
      rx="4"
      ry="3.80444"
      fill={
        (isGreen && ELLIPSE_GREEN_COLORS) ||
        (isOrange && ELLIPSE_ORANGE_COLORS) ||
        (isOrchid && ELLIPSE_ORCHID_COLORS) ||
        (isBlue && ELLIPSE_BLUE_COLORS)
      }
    />
  </svg>
)

export default IconEllipse
