import React, { useEffect, useState } from 'react'
import { useForm, Controller } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import { Typography, IconButton, InputAdornment, FormHelperText } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import { Box } from '@mui/material'
import DatePicker from '../../CustomDatePicker/CustomDatePicker'
import IconClose from '../../../../assets/Icons/IconClose/IconClose'
import useStyles from './FoundJobPopup.styles'
import { foundJobSchema } from '../../../../utils/schemas'
import api from '../../../../api/api'
import useAsync from '../../../../hooks/useAsync'
import {
  API_COMPANY_DIRECTORY_ENDPOINT,
  API_COMPANY_DIRECTORY_NOT_STUDENT_ENDPOINT,
  API_FOUND_JOB_ENDPOINT,
  API_STAFF_FOUND_JOB_ENDPOINT,
  MAX_COMMENT_SYMBOLS_FOUND_JOB_AND_WEEK_REPORT,
} from '../../../../utils/constants'
import IconCalendar from '../../../../assets/Icons/IconCalendar/IconCalendar'
import processServerError from '../../../../utils/server-error'
import { formatApiDate } from '../../../../utils/dates'
import CheckboxItem from '../../CheckboxGroup/CheckboxItem/CheckboxItem'
import { ControlledSelectField, ControlledTextField } from '../../FormElements'
import ControlledCompanyFieldFoundJob from './controlled-company-field-found-job/controlled-company-field-found-job'

export default function FoundJobPopup({
  openState = [],
  setSnack,
  studentToEdit,
  vacancy,
  setPrevPopup,
  updateVacancies,
}) {
  const [open, setOpen] = openState
  const [count, setCount] = useState(0)
  const [company, setCompany] = useState('')
  const [position, setPosition] = useState('')
  const [isPartnerVacancy, setIsPartnerVacancy] = useState(false)
  const [vacancyId, setVacancyId] = useState(undefined)
  const [vacancyName, setVacancyName] = useState('')
  const [isAgreeCheckbox, setIsAgreeCheckbox] = useState(false)

  const defaultValues = {
    offer_date: new Date(),
  }
  const { run } = useAsync({
    data: null,
    error: null,
  })
  const {
    control,
    register,
    handleSubmit,
    setValue,
    setError,
    reset,
    watch,
    clearErrors,
    resetField,
    formState: { errors },
  } = useForm({
    defaultValues,
    resolver: yupResolver(foundJobSchema),
    mode: 'onChange',
  })

  const resetStates = () => {
    setCount(0)
    clearErrors()
    setCompany('')
    setPosition('')
    if (!vacancy) setIsPartnerVacancy(false)
    setVacancyId(undefined)
    setVacancyName('')
    setIsAgreeCheckbox(false)
  }

  const handleCompanyChange = (e) => {
    setCompany(e.target.value)
    setValue('company_name', e.target.value)
  }

  const historyText = watch('employment_history')
  const positionValue = watch('job_name')

  const handlePositionChange = (e) => {
    if (isPartnerVacancy) {
      const selectedJob = company?.hr_vacancies?.find((job) => job.id === e.target.value)
      const jobName = selectedJob?.position
      setVacancyId(selectedJob?.id)
      setValue('job_name', jobName, { shouldValidate: true, shouldDirty: false })
    } else {
      setPosition(e.target.value)
      setValue('job_name', e.target.value, { shouldValidate: true, shouldDirty: false })
    }
  }

  useEffect(() => {
    register('employment_history')
    // TODO уточнить у бэка как будет называться поле, пока бэкенда нет
    // TODO  Но на новое поле не пятисотит
    register('is_agree')
  }, [register])

  const [employmentDate, setEmploymentDate] = useState(new Date())

  React.useEffect(() => {
    setCount(historyText?.length)
  }, [historyText])

  const classes = useStyles()

  const handleClose = () => {
    setOpen(false)
    if (vacancy) setPrevPopup(true)
    reset(defaultValues)
    resetStates()
  }

  // Закроем предыдущий попап, если он есть
  useEffect(() => {
    if (open && vacancy) setPrevPopup(false)
  }, [open])

  const onPostJob = async (data) => {
    // eslint-disable-next-line camelcase
    const { company_name, job_name, employment_history, is_agree } = data
    try {
      const response = await api.post(studentToEdit?.id ? API_STAFF_FOUND_JOB_ENDPOINT : API_FOUND_JOB_ENDPOINT, {
        offer_date: formatApiDate(employmentDate),
        company_name,
        // eslint-disable-next-line camelcase
        job_name: vacancy ? vacancyName : job_name,
        employment_history,
        profile_id: studentToEdit?.id,
        // eslint-disable-next-line no-nested-ternary
        partner_vacancy: isPartnerVacancy ? (vacancy ? vacancy.id : vacancyId) : undefined,
        is_agree,
      })
      if (response.status === 201) {
        setSnack({
          open: true,
          header: 'Форма отправлена',
          message: 'Поздравляем с получением работы!',
        })
        reset(defaultValues)
        setCompany('')
        setPosition(null)
        // обновляем список вакансий, если запрос от менеджера
        if (updateVacancies) updateVacancies()
        handleClose()
      }
      return Promise.resolve(response.data)
    } catch (e) {
      return processServerError(e, setError)
    }
  }

  const onSubmit = (data) => {
    run(onPostJob(data))
  }

  useEffect(() => {
    setEmploymentDate(new Date())
    setValue('offer_date', new Date())
    setValue('employment_history', '')
  }, [open])

  const onChangeCheckbox = (event) => {
    resetField('company_name')
    resetField('job_name')
    setCompany('')
    setPosition('')
    setVacancyId(undefined)
    setIsPartnerVacancy(event.target.checked)
  }

  useEffect(() => {
    if (vacancy) {
      setIsPartnerVacancy(true)
      setValue('company_name', vacancy.company_name, { shouldValidate: true, shouldDirty: false })
      setVacancyId(vacancy?.id)
      setCompany({
        hr_vacancies: [
          {
            position: vacancy.position,
            id: vacancy.id,
          },
        ],
      })
      setValue('job_name', vacancy.id, { shouldValidate: true, shouldDirty: false })
      setVacancyName(vacancy.position)
    }
  }, [vacancy])

  useEffect(() => {
    setValue('is_agree', isAgreeCheckbox)
  }, [isAgreeCheckbox])

  const onCompanyChange = (event, item) => {
    setCompany(item)
    setValue('company_name', item?.name || undefined, { shouldValidate: true, shouldDirty: false })
  }

  return (
    <div>
      <Dialog
        maxWidth="xl"
        classes={{ paper: classes.root }}
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <div className={classes.container}>
          <IconButton onClick={handleClose} className={classes.closePopup}>
            <IconClose />
          </IconButton>
          <DialogContent className={classes.dialogContent}>
            <form className={classes.form} onSubmit={handleSubmit(onSubmit)} id="form-job">
              <div className={classes.titleContainer}>
                <Typography variant="h2" id="form-dialog-title" className={classes.title}>
                  {studentToEdit ? 'Студент нашел работу' : 'Поздравляем с получением оффера!'}
                </Typography>
                <Typography variant="body1" color="textSecondary" style={{ width: '512px' }}>
                  {studentToEdit
                    ? 'Заполни все поля, чтобы изменить статус студента.'
                    : 'Расскажи нам больше о своем пути и процессе поиска работы. Ты всегда сможешь вернуться в программу трудоустройства и продолжить поиски, если компания тебе не подойдёт.'}
                </Typography>
              </div>
              <fieldset className={classes.fieldset}>
                <div className={classes.leftColumn}>
                  <Controller
                    name="isPartnerVacancy"
                    control={control}
                    render={({ field }) => (
                      <CheckboxItem
                        {...field}
                        checkboxLabel="Вакансия от партнёров"
                        checked={isPartnerVacancy}
                        margin={{ marginBottom: 24 }}
                        onChange={onChangeCheckbox}
                        disabled={!!vacancy}
                      />
                    )}
                  />
                  {!isPartnerVacancy && (
                    <ControlledTextField
                      labelTitle="Название компании"
                      fieldName="company_name"
                      value={company}
                      onChange={handleCompanyChange}
                      control={control}
                      error={!!errors.company_name?.message}
                      helperText={errors.company_name?.message}
                      placeholder="Введи название"
                      height="76px"
                    />
                  )}

                  {!isPartnerVacancy && (
                    <ControlledTextField
                      labelTitle="Название Должность"
                      fieldName="job_name"
                      value={position}
                      onChange={handlePositionChange}
                      control={control}
                      error={!!errors.job_name?.message}
                      helperText={errors.job_name?.message}
                      placeholder="Введи название"
                      height="76px"
                    />
                  )}

                  {isPartnerVacancy && (
                    <ControlledCompanyFieldFoundJob
                      control={control}
                      width={340}
                      error={!!errors.company_name?.message}
                      helperText={errors.company_name?.message}
                      endpoint={
                        studentToEdit ? API_COMPANY_DIRECTORY_NOT_STUDENT_ENDPOINT : API_COMPANY_DIRECTORY_ENDPOINT
                      }
                      resetCompany={() => resetField('company_name')}
                      disabled={!!vacancy}
                      height="96px"
                      onCompanyChange={onCompanyChange}
                    />
                  )}

                  {isPartnerVacancy && (
                    <ControlledSelectField
                      labelTitle="Должность"
                      tipContent="Выбери из списка"
                      value={positionValue}
                      error={!!errors?.job_name?.message}
                      helperText={errors?.job_name?.message}
                      onChange={handlePositionChange}
                      /* Мутируем объект, чтобы его правильно отрендерил компонент */
                      items={company?.hr_vacancies?.map((item) => ({ ...item, name: item.position }))}
                      maxWidth="340px"
                      disabled={!!vacancy}
                      defaultValue=""
                      height="96px"
                    />
                  )}

                  <div className={classes.fieldContainer}>
                    <Typography className={classes.dataLabel}>Дата первого рабочего дня</Typography>
                    <DatePicker
                      InputProps={{
                        startAdornment: (
                          <InputAdornment position="end">
                            <IconButton>
                              <IconCalendar isGray={!employmentDate} />
                            </IconButton>
                          </InputAdornment>
                        ),
                      }}
                      clearLabel="Удалить дату"
                      cancelLabel="Отменить"
                      okLabel="Сохранить"
                      inputVariant="outlined"
                      format="dd MMM yyyy"
                      disableToolbar
                      variant="dialog"
                      clearable
                      value={employmentDate}
                      error={errors.offer_date?.message}
                      helperText={errors.offer_date?.message || ' '}
                      onChange={(val) => {
                        if (val) {
                          setValue('offer_date', val, { shouldDirty: true })
                          setEmploymentDate(val)
                        } else {
                          setValue('offer_date', null, { shouldDirty: true })
                          setEmploymentDate(null)
                        }
                      }}
                    />
                  </div>
                </div>
                <div className={classes.leftColumn}>
                  <div className={`${classes.fieldContainer} ${classes.fieldContainerRight}`}>
                    <ControlledTextField
                      labelTitle="История трудоустройства"
                      fieldName="employment_history"
                      control={control}
                      error={!!errors.employment_history?.message}
                      helperText={errors.employment_history?.message}
                      placeholder="Расскажи нам о своём пути. Что произошло за это время?"
                      multiline
                      rows={13}
                      symbolsCount={count}
                      maxLength={MAX_COMMENT_SYMBOLS_FOUND_JOB_AND_WEEK_REPORT}
                      minLength={80}
                      maxWidth={407}
                    />
                  </div>
                </div>
              </fieldset>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: '15px', maxWidth: '751px' }}>
                <CheckboxItem
                  // checkboxLabel="Я готов(а) поделиться историей своего трудоустройства и даю согласие на использовании данных, указанных в этой форме, в маркетинговых целях Яндекс Практикума  "
                  checked={isAgreeCheckbox}
                  margin={{ marginBottom: 8 }}
                  onChange={() => setIsAgreeCheckbox(!isAgreeCheckbox)}
                />
                <Typography style={{ fontSize: '14px', fontFamily: 'YS Text' }}>
                  Я готов(а) поделиться историей своего трудоустройства и даю согласие на использовании данных,
                  указанных в этой форме, в маркетинговых целях Яндекс Практикума
                </Typography>
              </Box>
            </form>
            <FormHelperText error style={{ textAlign: 'center', margin: 0 }}>
              {errors.non_field_errors?.message || ' '}
            </FormHelperText>
            <div className={classes.buttonContainer}>
              <Button form="form-job" className={classes.submitButton} type="submit">
                Поделиться радостью
              </Button>
            </div>
          </DialogContent>
        </div>
      </Dialog>
    </div>
  )
}
