/* eslint-disable no-param-reassign */
import React, { useEffect, useRef, useState } from 'react'

import { Grid } from '@material-ui/core'
import IconButton from '@material-ui/core/IconButton'

import { DndProvider } from 'react-dnd'
import { HTML5Backend } from 'react-dnd-html5-backend'

import ColumnGrid from './ColumnGrid/ColumnGrid'
import IconSwiperArrow from '../../../../assets/Icons/IconSwiperArrow/IconSwiperArrow'
import NewResponsePopup from './NewResponsePopup/NewResponsePopup'
import useStyles from './Diary.style'
import { TEMP_STATUSES } from '../../../../utils/constants'
import { NewContactPopup } from '../../../Sub/GlobalPopups'
import NewStatusPopup from './NewStatusPopup/NewStatusPopup'

const Diary = ({ cards, states, handlers, currencies, readOnly, contactsForNewResponseState = false }) => {
  const classes = useStyles()
  const { openState, cardDataState } = states
  const { onPatchVacancy, onDeleteCard, onSubmitVacancy, setSnack } = handlers
  const [, setOpen] = openState
  const [cardData, setCardData] = cardDataState
  const [isNewReport, setIsNewReport] = useState(true)
  const [gridScrollLeft, _setGridScrollLeft] = useState(0)
  const [openNewContact, setOpenNewContact] = useState(false)
  const [contactToEdit, setContactToEdit] = useState(undefined)
  const [maxWidth, setMaxWidth] = useState(() => 2350 - window.innerWidth)
  const gridRef = useRef(null)

  const statuses = TEMP_STATUSES

  const setGridScrollLeft = (value) => {
    const min = 0
    const max = maxWidth
    // eslint-disable-next-line no-nested-ternary
    const result = value < min ? min : value > max ? max : value
    return _setGridScrollLeft(result)
  }

  const handleOpenNewResponsePopup = () => {
    setIsNewReport(true)
    setOpen(true)
  }

  const handleOpenPreviewPopup = () => {
    setIsNewReport(false)
    setOpen(true)
  }

  const handleToggleEditPopup = () => {
    setIsNewReport(false)
    setOpen(true)
  }

  const handleSetCardData = (card) => {
    setCardData(card)
  }

  const filterAndSort = (cardList, status) => {
    // Отфильтровали карты
    const filteredCards = cardList.filter((value) => value.status_id === status.status_id)
    // Теперь надо их отсортировать по дате, причём по дате статуса
    let sortedCards = []
    switch (status.status_id) {
      case 'response':
        sortedCards = filteredCards.sort((a, b) => {
          const dateA = new Date(a.response_date)
          const dateB = new Date(b.response_date)
          return dateB - dateA
        })
        break
      case 'hr_interview':
        sortedCards = filteredCards.sort((a, b) => {
          const dateA = new Date(a.hr_interview_date)
          const dateB = new Date(b.hr_interview_date)
          return dateB - dateA
        })
        break
      case 'test_task':
        sortedCards = filteredCards.sort((a, b) => {
          const dateA = new Date(a.test_task_date)
          const dateB = new Date(b.test_task_date)
          return dateB - dateA
        })
        break
      case 'tech_interview':
        sortedCards = filteredCards.sort((a, b) => {
          const dateA = new Date(a.tech_interview_date)
          const dateB = new Date(b.tech_interview_date)
          return dateB - dateA
        })
        break
      case 'boss_interview':
        sortedCards = filteredCards.sort((a, b) => {
          const dateA = new Date(a.boss_interview_date)
          const dateB = new Date(b.boss_interview_date)
          return dateB - dateA
        })
        break
      case 'offer':
        sortedCards = filteredCards.sort((a, b) => {
          const dateA = new Date(a.offer_date)
          const dateB = new Date(b.offer_date)
          return dateB - dateA
        })
        break
      case 'rejection':
        sortedCards = filteredCards.sort((a, b) => {
          const dateA = new Date(a.rejection_date)
          const dateB = new Date(b.rejection_date)
          return dateB - dateA
        })
        break
      default:
        break
    }
    return sortedCards
  }

  const scroll = (offset) => {
    const easeInOutQuad = (t, b, c, d) => {
      t /= d / 2
      if (t < 1) return (c / 2) * t * t + b
      // eslint-disable-next-line no-plusplus
      t--
      return (-c / 2) * (t * (t - 2) - 1) + b
    }
    const duration = 250
    const change = offset
    const start = gridScrollLeft
    let currentTime = 0
    const increment = 20

    const animateScroll = () => {
      currentTime += increment
      const val = easeInOutQuad(currentTime, start, change, duration)
      setGridScrollLeft(val)
      if (currentTime < duration) {
        setTimeout(animateScroll, increment)
      }
    }
    animateScroll()
  }

  useEffect(() => {
    gridRef.current.scrollLeft = gridScrollLeft
  }, [gridScrollLeft])

  useEffect(() => {
    function handleResize() {
      setMaxWidth(2350 - window.innerWidth)
    }

    window.addEventListener('resize', handleResize)
    return () => window.removeEventListener('resize', handleResize)
  }, [])

  const closeResponsesPopup = () => {
    setOpen(false)
    setCardData(null)
    setIsNewReport(true)
  }

  const handleBackNewContact = () => {
    setOpenNewContact(false)
    setOpen(true)
  }

  const handleOpenNewContact = () => {
    setOpen(false)
    setOpenNewContact(true)
  }

  return (
    <>
      <NewStatusPopup />
      <NewResponsePopup
        course_id={cards[0]?.course_id}
        openState={openState}
        data={isNewReport ? null : cardData}
        onSubmit={isNewReport ? onSubmitVacancy : onPatchVacancy}
        onDeleteCard={isNewReport ? null : onDeleteCard}
        isNewReport={isNewReport}
        setSnack={setSnack}
        currencies={currencies}
        readOnly={readOnly}
        closeResponsesPopup={closeResponsesPopup}
        openNewContact={handleOpenNewContact}
        contactsForNewResponseState={contactsForNewResponseState}
        setContactToEdit={setContactToEdit}
      />
      <NewContactPopup
        cardData={isNewReport ? null : cardData}
        openState={[openNewContact, setOpenNewContact]}
        setSnack={setSnack}
        onBackClick={handleBackNewContact}
        isNewReport={isNewReport}
        contactsForNewResponseState={contactsForNewResponseState}
        data={contactToEdit}
        clearData={() => setContactToEdit(undefined)}
      />
      {gridScrollLeft > 0 && (
        <IconButton className={classes.prevButton} onClick={() => scroll(-305)}>
          <IconSwiperArrow left />
        </IconButton>
      )}
      {gridScrollLeft < maxWidth && (
        <IconButton className={classes.nextButton} onClick={() => scroll(305)}>
          <IconSwiperArrow />
        </IconButton>
      )}
      <Grid className={classes.mainGrid} ref={gridRef} onScroll={() => setGridScrollLeft(gridRef.current.scrollLeft)}>
        <DndProvider backend={HTML5Backend}>
          {statuses.map((status) => (
            <ColumnGrid
              key={status.status_id}
              openResponsePopup={handleOpenNewResponsePopup}
              status={status}
              cards={filterAndSort(cards, status)}
              handleToggleEditPopup={handleToggleEditPopup}
              handleSetCardData={handleSetCardData}
              handleOpenPreviewPopup={handleOpenPreviewPopup}
              readOnly={readOnly}
            />
          ))}
        </DndProvider>
      </Grid>
    </>
  )
}
export default Diary
