import React, { useCallback, useState } from 'react'

import { IconButton, FormHelperText } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import Dialog from '@mui/material/Dialog'
import DialogContent from '@mui/material/DialogContent'
import DialogActions from '@mui/material/DialogActions'
import useStyles from './EditProfilePopup.styles'

import IconClose from '../../../../assets/Icons/IconClose/IconClose'

import TabPanel from '../../FormElements/tab-panel/tab-panel'
import FirstTabProfile from './FirstTabProfile'
import SecondTabProfile from './SecondTabProfile'
import Tabs from '../../FormElements/custom-tabs/custom-tabs'
import Tab from '../../FormElements/custom-tab/custom-tab'
import useEditProfile from './useEditProfile'

function EditProfilePopup({ openState = [], setSnack, studentToEdit = undefined }) {
  const [open] = openState
  const [tabValue, setTabValue] = useState(0)

  const classes = useStyles()

  const {
    handleClose,
    onSubmit,
    firstTabValues,
    secondTabValues,
    firstTabHandlers,
    secondTabHandlers,
    validationError,
  } = useEditProfile(openState, setSnack, studentToEdit, tabValue)

  const handleTabChange = useCallback((event, newValue) => {
    setTabValue(newValue)
  }, [])

  return (
    <div>
      <Dialog maxWidth="xl" open={open} onClose={handleClose} className={classes.root} scroll="body">
        <IconButton onClick={handleClose} className={classes.closePopup}>
          <IconClose />
        </IconButton>
        <DialogContent className={classes.dialogContent}>
          <form className={classes.form} onSubmit={onSubmit} id="form-edit-profile">
            <Tabs value={tabValue} onChange={handleTabChange}>
              <Tab label="Информация профиля" />
              <Tab label="Резюме и портфолио" />
            </Tabs>
            <TabPanel value={tabValue} index={0}>
              <FirstTabProfile values={firstTabValues} handlers={firstTabHandlers} />
            </TabPanel>
            <TabPanel value={tabValue} index={1}>
              <SecondTabProfile values={secondTabValues} handlers={secondTabHandlers} />
            </TabPanel>
          </form>
        </DialogContent>
        <DialogActions className={classes.dialogActions}>
          <FormHelperText error style={{ textAlign: 'center', margin: '5px 0 12px 0' }}>
            {validationError || ' '}
          </FormHelperText>
          <Button form="form-edit-profile" className={classes.submitButton} type="submit">
            Сохранить
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}

export default React.memo(EditProfilePopup)
