/* eslint-disable camelcase */
import React, { Dispatch, FC, SetStateAction, useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import {
  Typography,
  IconButton,
  Popover,
  FormHelperText,
  ThemeProvider,
  Button,
  Dialog,
  DialogContent,
  DialogTitle,
  Box,
} from '@mui/material'
import IconClose from '../../../../assets/Icons/IconClose/IconClose'
import useStyles from './NewReportPopup.styles'
import { newReportSchema } from '../../../../utils/schemas'
import api from '../../../../api/api'
import useAsync from '../../../../hooks/useAsync'
import { API_REPORTS_ENDPOINT, API_GET_WEEK_STATS } from '../../../../utils/constants'
import IconClipInfo from '../../../../assets/Icons/IconClipInfo/IconClipInfo'
import IconEllipse from '../../../../assets/Icons/IconEllipse/IconEllipse'
import processServerError from '../../../../utils/server-error'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { setProfile } from '../../../../services/slices/auth-slice'
import DatePicker from '../../CustomDatePicker/CustomDatePicker'
import ControlledTextField from '../../FormElements/controlled-text-field/controlled-text-field'
import theme from '../../../../utils/themeMUI5'
import { enqueueSnackbar } from '../../../../services/slices/app-slice'
import { formatApiDate } from '../../../../utils/dates'

type TReport = {
  boss_interview: number | undefined
  hr_interview: number | undefined
  offer: number | undefined
  rejection: number | undefined
  reportTotal: number | undefined
  report_date: string
  response: number | undefined
  tech_interview: number | undefined
  test_task: number | undefined
}

type TCurrentReport = TReport & {
  comment: string
  id: number
  profile: {
    first_name: string
    course: {
      id: number
    }
    cohort_number: number
  }
  cohort_number: number
  course: { id: 1; name: 'Python-Разработчик' }
  first_name: string
  image: null | string
}

type TProgress = {
  report: TReport
}

type TFieldsData = {
  [n: string]: string
}

type TWeekStats = Omit<TReport, 'reportTotal'>

type Props = {
  open: boolean
  setOpen: React.Dispatch<React.SetStateAction<boolean>>
  setSnack: Dispatch<SetStateAction<{ open: boolean; header: string; message: string }>>
  refreshFeeds: () => void
  currentReport: TCurrentReport
  openFoundJobPopup: () => void
}

type TFormData = {
  report_date: Date
  comment: string
  non_field_errors: string
  response: number
  test_task: number
  tech_interview: number
  offer: number
}

type TAchievement = {
  name: string
  condition_text: string
  success_image: string
  success_condition: number
}

const Progress = ({ report }: TProgress) => (
  <Box
    sx={{
      width: 350,
    }}
  >
    <Box
      sx={{
        width: '100%',
        position: 'relative',
        display: 'flex',
      }}
    >
      <Box
        sx={{
          background: '#87CC9E',
          width: `${(Number(report.response) / Number(report.reportTotal)) * 100}%`,
          minHeight: 12,
          borderRadius: '12px',
          position: 'relative',
          zIndex: '4',
        }}
      />
      <Box
        sx={{
          zIndex: '3',
          borderRadius: '12px',
          position: 'absolute',
          left: `calc(${(Number(report.response) / Number(report.reportTotal)) * 100}% - 9px)`,
          background: '#FFCE92',
          width: `${(Number(report.test_task) / Number(report.reportTotal)) * 100}%`,
          minHeight: 12,
        }}
      />
      <Box
        sx={{
          zIndex: '2',
          borderRadius: '12px',
          position: 'absolute',
          left: `calc(${
            ((Number(report.test_task) + Number(report.response)) / Number(report.reportTotal)) * 100
          }% - 18px)`,
          background: '#7F67D2',
          width: () => {
            if (report.offer) {
              return `${(Number(report.tech_interview) / Number(report.reportTotal)) * 100}%`
            }
            return `calc(${(Number(report.tech_interview) / Number(report.reportTotal)) * 100}% + 18px)`
          },
          minHeight: 12,
        }}
      />
      <Box
        sx={{
          zIndex: '1',
          borderRadius: '12px',
          position: 'absolute',
          background: '#5A9BFF',
          left: `calc(${
            ((Number(report.tech_interview) + Number(report.response) + Number(report.test_task)) /
              Number(report.reportTotal)) *
            100
          }% - 26px)`,
          width: `${(Number(report.offer) / Number(report.reportTotal)) * 100}%`,
          minHeight: 12,
        }}
      />
    </Box>
  </Box>
)

const NewReportPopup: FC<Props> = ({ open, setOpen, setSnack, refreshFeeds, currentReport, openFoundJobPopup }) => {
  const dispatch = useAppDispatch()
  const { profile } = useAppSelector((store) => store.auth)
  const [anchorEl, setAnchorEl] = useState<HTMLElement | null>(null)
  const [weekStats, setWeekStats] = useState<TWeekStats>({
    response: 0,
    hr_interview: 0,
    test_task: 0,
    tech_interview: 0,
    boss_interview: 0,
    offer: 0,
    rejection: 0,
    report_date: '01.01.1970',
  })
  const [reportTotal, setReportTotal] = useState<number | undefined>(0)
  const handleClickStats = (event: React.SyntheticEvent) => {
    setAnchorEl(event.currentTarget as HTMLElement)
  }

  const handleCloseStats = () => {
    setAnchorEl(null)
  }

  const handleOpenFoundJobPopup = () => {
    setOpen(false)
    openFoundJobPopup()
  }

  const openPopover = Boolean(anchorEl)

  const defaultValues = {}
  const { run } = useAsync({
    data: null,
    error: null,
  })
  const {
    control,
    handleSubmit,
    setValue,
    formState: { errors },
    watch,
    setError,
    reset,
    clearErrors,
  } = useForm<TFormData>({
    defaultValues,
    resolver: yupResolver(newReportSchema),
    mode: 'onChange',
  })

  const commentState = watch('comment')

  const [reportDate, setReportDate] = useState<Date | null>(new Date())
  const classes = useStyles()

  const navigate = useNavigate()

  const onOpenPopup = async () => {
    if (currentReport) {
      const {
        response,
        hr_interview,
        test_task,
        tech_interview,
        boss_interview,
        offer,
        rejection,
        report_date,
        comment,
      } = currentReport
      setWeekStats({ response, hr_interview, test_task, tech_interview, boss_interview, offer, rejection, report_date })
      setReportTotal(Number(test_task) + Number(offer) + Number(response) + Number(tech_interview))
      setReportDate(new Date(report_date))
      setValue('comment', comment, { shouldValidate: true, shouldDirty: true })
      return 0
    }
    try {
      const res = await api.get(API_GET_WEEK_STATS)
      if (res.status === 200) {
        setWeekStats(res.data)
        setReportTotal(res.data.test_task + res.data.offer + res.data.response + res.data.tech_interview)
      }
      return Promise.resolve(res.data)
    } catch (e) {
      return processServerError(e, setError)
    }
  }

  // eslint-disable-next-line camelcase
  useEffect(() => {
    if (reportDate) {
      setValue('report_date', reportDate, {
        shouldValidate: true,
        shouldDirty: true,
      })
    }
    if (open) {
      run(onOpenPopup())
    }
  }, [open])

  const handleClose = () => {
    reset(defaultValues)
    const today = new Date()
    setReportDate(today)
    setValue('report_date', today, {
      shouldValidate: true,
      shouldDirty: true,
    })
    clearErrors()
    setOpen(false)
    clearErrors()
    reset(defaultValues)
  }

  const handleBackToDiary = () => {
    navigate('/diary')
    handleClose()
  }

  const onPostReport = async (data: TFormData) => {
    if (currentReport) {
      // eslint-disable-next-line camelcase
      const { comment } = data
      try {
        const res = await api.patch(`${API_REPORTS_ENDPOINT}/${currentReport.id}`, {
          comment,
        })
        if (res.status === 200) {
          await dispatch(setProfile({ ...profile, last_report_update_datetime: formatApiDate(new Date()) }))
          dispatch(
            enqueueSnackbar({
              info: {
                type: 'common',
                title: 'Форма отправлена',
                message: 'Спасибо',
              },
              options: {
                key: new Date().getTime() + Math.random(),
                autoHideDuration: 6000,
              },
            }),
          )
          if (refreshFeeds) refreshFeeds()
          handleClose()
        }
        return Promise.resolve(res.data)
      } catch (e) {
        return processServerError(e, setError)
      }
    } else {
      // eslint-disable-next-line camelcase
      const { response, test_task, tech_interview, offer, comment } = data
      try {
        const res = await api.post(API_REPORTS_ENDPOINT, {
          response,
          test_task,
          tech_interview,
          offer,
          comment,
        })
        if (res.status === 201) {
          await dispatch(setProfile({ ...profile, last_report_update_datetime: formatApiDate(new Date()) }))
          dispatch(
            enqueueSnackbar({
              info: {
                type: 'common',
                title: 'Форма отправлена',
                message: 'Спасибо',
              },
              options: {
                key: new Date().getTime() + Math.random(),
                autoHideDuration: 6000,
              },
            }),
          )
          if (refreshFeeds) refreshFeeds()
          handleClose()
        }
        return Promise.resolve(res.data)
      } catch (e) {
        return processServerError(e, setError)
      }
    }
  }

  const onSubmit = (data: TFormData) => {
    run(onPostReport(data))
  }

  return (
    <ThemeProvider theme={theme}>
      <Dialog
        maxWidth="xl"
        className={classes.root}
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <IconButton onClick={handleClose} className={classes.closePopup}>
          <IconClose />
        </IconButton>
        <DialogTitle mb="8px" className={classes.dialogTitle}>
          Расскажи, как прошла твоя неделя
        </DialogTitle>
        <Typography color={theme.color.black500} variant="body3">
          Твою запись с количеством показателей и комментарием увидят другие студенты в их Ленте. Мы рекомендуем
          заполнять форму хотя бы один раз в неделю.
        </Typography>
        <DialogContent className={classes.dialogContent}>
          <form className={classes.form} onSubmit={handleSubmit(onSubmit)} id="form">
            <Box className={classes.titleContainer} />
            <fieldset className={classes.fieldset}>
              <Box className={classes.dateAndStatisticContainer}>
                <Box className={`${classes.fieldContainer} ${classes.fieldContainerDate}`}>
                  <Typography>Дата отчёта</Typography>
                  <DatePicker
                    clearLabel="Удалить дату"
                    cancelLabel="Отменить"
                    format="dd.MM.yyyy"
                    disableToolbar
                    readOnly
                    InputProps={{
                      disableUnderline: true,
                    }}
                    value={reportDate}
                    error={errors.report_date?.message}
                    helperText={errors.report_date?.message}
                    onChange={(val: Date) => {
                      if (val) {
                        setValue('report_date', val, { shouldDirty: true })
                        setReportDate(val)
                      } else {
                        setValue('report_date', val, { shouldDirty: true })
                        setReportDate(null)
                      }
                    }}
                  />
                </Box>
                <Box className={classes.statisticContainer}>
                  <Typography>Статистика</Typography>
                  <Box className={classes.reportStatsContainer}>
                    <Progress report={{ ...weekStats, reportTotal }} />
                    <Typography
                      variant="body2"
                      ml="5px"
                    >{`${weekStats.response} / ${weekStats.test_task} / ${weekStats.tech_interview} / ${weekStats.offer}`}</Typography>
                    <IconButton onClick={handleClickStats} sx={{ padding: 0, marginLeft: '12px' }}>
                      <IconClipInfo />
                    </IconButton>
                    <Popover
                      open={openPopover}
                      anchorEl={anchorEl}
                      onClose={handleCloseStats}
                      anchorOrigin={{
                        vertical: 'top',
                        horizontal: 'left',
                      }}
                      transformOrigin={{
                        vertical: 'bottom',
                        horizontal: 'left',
                      }}
                    >
                      <ul className={classes.legend}>
                        <li className={classes.legendItem}>
                          {' '}
                          <IconEllipse isGreen isOrange={undefined} isOrchid={undefined} isBlue={undefined} />
                          <Typography>Отклики на вакансии</Typography>
                        </li>
                        <li className={classes.legendItem}>
                          <IconEllipse isOrange isGreen={undefined} isOrchid={undefined} isBlue={undefined} />
                          <Typography>Тестовые задания</Typography>
                        </li>
                        <li className={classes.legendItem}>
                          <IconEllipse isOrchid isGreen={undefined} isOrange={undefined} isBlue={undefined} />
                          <Typography>Технические собеседования</Typography>
                        </li>
                        <li className={classes.legendItem}>
                          <IconEllipse isBlue isGreen={undefined} isOrange={undefined} isOrchid={undefined} />
                          <Typography>Офферы</Typography>
                        </li>
                      </ul>
                    </Popover>
                  </Box>
                </Box>
              </Box>
              <ControlledTextField
                labelTitle="Комментарий"
                fieldName="comment"
                control={control}
                error={!!errors.comment?.message}
                helperText={errors.comment?.message || ' '}
                placeholder="Введи текст"
                multiline
                rows={6}
                symbolsCount={commentState?.length ?? 0}
                minLength={80}
                maxLength={500}
                maxWidth={610}
                marginLeft="-2px"
              />
            </fieldset>
            <Button className={classes.foundJobBtn} onClick={handleOpenFoundJobPopup}>
              Нашёл работу?
            </Button>
          </form>
          <FormHelperText error style={{ textAlign: 'center', margin: '5px 0' }}>
            {errors.report_date?.message || errors.non_field_errors?.message || ' '}
          </FormHelperText>
          <Box className={classes.buttonContainer}>
            <Button sx={{ width: 160 }} variant="outlined" onClick={handleBackToDiary}>
              Перейти в дневник
            </Button>
            <Button sx={{ width: 160, fontSize: '14px !important' }} form="form" type="submit">
              Отправить запись
            </Button>
          </Box>
        </DialogContent>
      </Dialog>
    </ThemeProvider>
  )
}

export default NewReportPopup
