/* eslint-disable jsx-a11y/label-has-associated-control */
import React from 'react'
import { FormHelperText, Avatar, Button } from '@mui/material/'
import useStyles from '../styles'

interface ControlledAvatarFieldProps {
  avatar: string | null
  helperText?: string
  onChange: (event: React.ChangeEvent<HTMLInputElement>) => void
}

const ControlledAvatarField = ({ avatar, helperText, onChange }: ControlledAvatarFieldProps) => {
  const classes = useStyles()

  return (
    <div className={classes.profileContainer}>
      <div className={classes.avatarContainerInputs}>
        <Avatar src={avatar ?? undefined} alt="Аватар пользователя" className={classes.iconUser} />
        <input
          name="image"
          accept="image/png,image/jpeg,image/pjpeg"
          className={classes.input}
          style={{ display: 'none' }}
          id="raised-button-file"
          type="file"
          onChange={onChange}
        />
        <label htmlFor="raised-button-file">
          <Button component="span" className={classes.uploudTitle}>
            Загрузить фото профиля
          </Button>
        </label>
      </div>
      <FormHelperText className={classes.avatarErrors}>{helperText || ' '}</FormHelperText>
    </div>
  )
}

export default React.memo(ControlledAvatarField)
