import React, { FC } from 'react'
import { Control, Controller, FieldError } from 'react-hook-form'
import { FormHelperText, Typography } from '@mui/material/'
import { CityIdSearchBar } from '../../Searchers'
import useStyles from '../styles'

interface Props {
  control: Control<any>
  error: boolean
  helperText?: string | null | FieldError
  width?: string | number
  hideLabel?: boolean
}

const ControlledCityIdField: FC<Props> = ({ control, error, helperText, width, hideLabel = undefined }) => {
  const classes = useStyles()

  return (
    <div className={classes.fieldContainer} style={{ width }}>
      {!hideLabel && <Typography variant="body2">Город</Typography>}
      <Controller
        name="city"
        control={control}
        render={({ field }) => <CityIdSearchBar value={field?.value} onChange={field?.onChange} error={error} />}
      />
      <div className={classes.input}>
        <FormHelperText error>{helperText || ' '}</FormHelperText>
      </div>
    </div>
  )
}

export default React.memo(ControlledCityIdField)
