import * as React from 'react'
import Tooltip from '@mui/material/Tooltip'
import useStyles from './ContactsInfo.style'

function ContactsInfo({ contact }) {
  const classes = useStyles()
  return (
    <Tooltip title={contact} classes={{ tooltip: classes.tooltip }} enterDelay={500} leaveDelay={200}>
      <span className={classes.contact}>{contact}</span>
    </Tooltip>
  )
}

export default ContactsInfo
