import React, { useState, useEffect, useMemo } from 'react'
import { format } from 'date-fns'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import { editFormSchema, emailOption, telegramOption } from '../../../../utils/schemas'
import api from '../../../../api/api'
import useAsync from '../../../../hooks/useAsync'
import noAvatar from '../../../../assets/nopic.jpg'

import {
  API_ONBOARDING_DIRECTORY_ENDPOINT,
  API_STUDENT_BY_ID_ENDPOINT,
  API_USER_PROFILE_ENDPOINT,
} from '../../../../utils/constants'

import processServerError from '../../../../utils/server-error'
import telegramIcon from '../../../../assets/telegram-icon.svg'
import emailIcon from '../../../../assets/email-icon.svg'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { setProfile } from '../../../../services/slices/auth-slice'
import { enqueueSnackbar } from '../../../../services/slices/app-slice'

const comMethods = [
  {
    id: 1,
    name: 'Telegram',
    icon: telegramIcon,
  },
  {
    id: 2,
    name: 'Email',
    icon: emailIcon,
  },
]

function useEditProfile(openState, setSnack, studentToEdit, tabValue) {
  const dispatch = useAppDispatch()
  const { profile } = useAppSelector((store) => store.auth)
  const [open, setOpen] = openState
  const [defaultCohortId, setDefaultCohortId] = useState(0)
  const [course, setCourse] = useState([])
  const [cohort, setCohort] = useState([])
  const [education, setEducation] = useState([])
  const [experience, setExperience] = useState([])
  const [avatar, setAvatar] = useState(noAvatar)
  const [date, setDate] = useState(new Date())
  const [validationSchema, setValidationSchema] = useState(editFormSchema)

  const addedFiles = useState(profile?.resume ? [...profile?.resume] : [])
  const [droppedFiles, setDroppedFiles] = addedFiles
  const deletedFiles = useState([])
  const [deletedId, setDeletedFiles] = deletedFiles
  const [isFilesChanged, setIsFilesChanged] = useState(false)

  const { run, data } = useAsync({
    data: { course: [], education: [] },
    error: null,
  })

  const { run: runPatch } = useAsync({
    data: null,
    error: null,
  })

  const {
    control,
    handleSubmit,
    register,
    setValue,
    getValues,
    watch,
    setError,
    reset,
    formState: { errors },
  } = useForm({
    mode: 'onChange',
    defaultValues: useMemo(() => {
      if (profile?.course) {
        return {
          ...profile,
          // course: profile?.course.id,
          course: profile?.course,
          contact_type: profile?.contact_type || 1,
          contact_info: profile?.contact_info || '',
          portfolio_url: profile?.portfolio_url,
        }
      }
      return {}
    }, [profile?.id]),
    resolver: useMemo(() => yupResolver(validationSchema), [validationSchema]),
  })

  const resetDropzone = () => {
    setDeletedFiles([])
    setDroppedFiles(profile?.resume ? [...profile?.resume] : [])
    setIsFilesChanged(false)
  }

  useEffect(() => {
    reset({
      ...profile,
      // course: profile?.course.id,
      course: profile?.course,
      contact_type: profile?.contact_type || 1,
      contact_info: profile?.contact_info || '',
      portfolio_url: profile?.portfolio_url,
    })
    resetDropzone()
  }, [profile]) // TODO: в массиве зависимостей должны быть примитивы, не объекты

  const getData = async () => {
    try {
      const response = await api.get(API_ONBOARDING_DIRECTORY_ENDPOINT)
      return Promise.resolve(response.data)
    } catch (e) {
      return Promise.reject(e.response)
    }
  }

  const methodValue = watch('contact_type')

  useEffect(() => {
    if (methodValue) {
      const scheme = editFormSchema.omit(['contact_info'])
      const option = methodValue === 1 ? telegramOption : emailOption('Неверный формат почты')
      setValidationSchema(scheme.shape({ contact_info: option }))
    }
  }, [methodValue])

  const value = getValues('birth_date')

  useEffect(() => {
    if (profile?.last_name) {
      const choosenCourse = course?.find((item) => item.id === profile?.course.id)
      const cohortsInCourse = choosenCourse?.cohorts
        ? choosenCourse.cohorts
            ?.filter((c) => c?.status === 'Активный')
            .map((item) => ({ id: item?.id, name: item?.cohort_number }))
        : null
      setCohort(cohortsInCourse?.sort((a, b) => +a?.name - +b?.name))
      setValue('cohort_number', null, { shouldValidate: true, shouldDirty: false })
      try {
        const cohortNumber = cohortsInCourse?.find((item) => item.name === profile?.cohort_number).id
        setValue('cohort_number', cohortNumber, { shouldValidate: true, shouldDirty: false })
        setDefaultCohortId(cohortNumber)
      } catch {
        setValue('cohort_number', null, { shouldValidate: true, shouldDirty: false })
      }
    }
  }, [course, open, profile?.last_name])

  useEffect(() => {
    setDate(value || null)
  }, [setDate, value])

  useEffect(() => {
    if (data?.courses) {
      const sortedCourses = [...data.courses].sort((a, b) => {
        if (a.name < b.name) {
          return -1
        }
        if (a.name > b.name) {
          return 1
        }
        return 0
      })
      setCourse(sortedCourses)
      setEducation(data.education)
      setExperience(data.experience)
    }
  }, [data]) // TODO: в массиве зависимостей должны быть примитивы, не объекты

  useEffect(() => {
    register('gender')
    register('birth_date')
    register('course')
    register('education_type')
    register('image')
    register('cohort_number')
    register('experience_type')
  }, [register])

  useEffect(() => run(getData()), [])

  useEffect(() => {
    if (studentToEdit?.last_name) {
      dispatch(setProfile(studentToEdit)) // TODO: не стоит записывать в стейт текущего студента для редактирования
    }
  }, [studentToEdit?.last_name])

  useEffect(() => {
    if (profile) {
      setAvatar(profile.image ? profile.image : noAvatar)
      setDate(new Date(profile.birth_date))
    }
  }, [profile]) // TODO: в массиве зависимостей должны быть примитивы, не объекты

  const genderValue = watch('gender')
  const courseValue = watch('course')
  const cohortValue = watch('cohort_number')
  const degreeValue = watch('education_type')
  const experienceValue = watch('experience_type')

  const handleChangeGender = (e) => {
    setValue('gender', e.target.value, true)
  }
  const handleBirthdayChange = (dt) => setValue('birth_date', dt, { shouldValidate: true, shouldDirty: false })
  const handleChangeDegree = (e) =>
    setValue('education_type', e.target.value, { shouldValidate: true, shouldDirty: false })
  const handleChangeExperience = (e) =>
    setValue('experience_type', e.target.value, { shouldValidate: true, shouldDirty: false })

  const handleChangeCohortAfterCourse = () => {
    const choosenCourse = course.find((item) => item.id === courseValue?.id)
    const cohortsInCourse = choosenCourse?.cohorts
      ? choosenCourse.cohorts
          ?.filter((c) => c?.status === 'Активный')
          .map((item) => ({ id: item?.id, name: item?.cohort_number }))
      : null

    setCohort(cohortsInCourse?.sort((a, b) => +a?.name - +b?.name))
    if (courseValue?.id !== profile?.course?.id) {
      setValue('cohort_number', undefined, { shouldValidate: true, shouldDirty: false })
    }
  }

  useEffect(() => {
    handleChangeCohortAfterCourse()
  }, [courseValue])

  const handleChangeCohort = (e) => {
    setValue('cohort_number', e.target.value, { shouldValidate: true, shouldDirty: false })
  }
  const handleImageChange = (e) => {
    const newAva = URL.createObjectURL(e.target.files[0])
    setAvatar(newAva)
    setValue('image', e.target.files[0], { shouldValidate: true, shouldDirty: false })
  }

  const handleMethodChange = (e) => {
    setValue('contact_type', e.target.value, { shouldValidate: true, shouldDirty: false })
    setValue('contact_info', '', { shouldValidate: true, shouldDirty: false })
  }

  const handleClose = () => {
    setOpen(false)
    reset()
    resetDropzone()
    setValue('image', null)
    setAvatar(noAvatar)
  }

  const onPatchProfile = async (formData) => {
    try {
      const response = await api.patch(
        studentToEdit ? API_STUDENT_BY_ID_ENDPOINT(studentToEdit.id) : API_USER_PROFILE_ENDPOINT,
        formData,
        {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        },
      )
      if (response.status === 200) {
        if (!studentToEdit) {
          dispatch(setProfile(response.data))
          setOpen(false)
          dispatch(
            enqueueSnackbar({
              info: {
                type: 'common',
                title: 'Форма отправлена',
                message: 'Профиль успешно обновлён',
              },
              options: {
                key: new Date().getTime() + Math.random(),
                autoHideDuration: 6000,
              },
            }),
          )
          setDeletedFiles([])
          setDroppedFiles([...response.data.resume])
          setIsFilesChanged(false)
          setValue('image', null)
        } else {
          setOpen(false)
          dispatch(
            enqueueSnackbar({
              info: {
                type: 'common',
                title: 'Форма отправлена',
                message: 'Профиль успешно обновлён',
              },
              options: {
                key: new Date().getTime() + Math.random(),
                autoHideDuration: 6000,
              },
            }),
          )
          setIsFilesChanged(false)
          setDeletedFiles([])
        }
      }
      return Promise.resolve(response.data)
    } catch (e) {
      return processServerError(e, setError)
    }
  }

  const onSubmit = (formData) => {
    let isChanged = false
    isChanged = isChanged || isFilesChanged
    function getFormData(object) {
      const fd = new FormData()
      Object.keys(object).forEach((key) => {
        if (object[key] == null) return
        fd.append(key, object[key])
        isChanged = true
      })
      return fd
    }
    const birthday = format(new Date(date), 'yyyy-MM-dd')
    const newState = {
      first_name: formData.first_name !== profile.first_name ? formData.first_name : undefined,
      last_name: formData.last_name !== profile.last_name ? formData.last_name : undefined,
      gender: formData.gender !== profile.gender ? formData.gender : undefined,
      city: +formData?.city?.id !== profile?.city?.id ? +formData?.city?.id : undefined,
      birth_date: birthday !== profile.birth_date ? birthday : undefined,
      cohort_number: +formData.cohort_number !== +defaultCohortId ? +formData.cohort_number : undefined,
      course: +formData.course?.id !== +profile.course.id ? formData.course.id : undefined,
      experience_type: +formData.experience_type !== +profile.experience_type ? formData.experience_type : undefined,
      previous_job_position:
        formData.previous_job_position !== profile.previous_job_position ? formData.previous_job_position : undefined,
      education_type: +formData.education_type !== +profile.education_type ? +formData.education_type : undefined,
      institution: formData.institution !== profile.institution ? formData.institution : undefined,
      image: formData.image !== profile.image ? formData.image : undefined,
      contact_type: +formData.contact_type !== +profile.contact_type ? formData.contact_type : undefined,
      contact_info: formData.contact_info !== profile.contact_info ? formData.contact_info : undefined,
      portfolio_url: formData.portfolio_url !== profile.portfolio_url ? formData.portfolio_url : undefined,
    }

    const fd = getFormData(newState)
    let index = 0
    deletedId?.forEach((file) => {
      fd.append(`resume[${index}]id`, file.id)
      index += 1
    })

    droppedFiles.forEach((file) => {
      // Рассчитываем смещение записи
      // В стейте могут быть не файлы, а записи о них. Их не надо добавлять на отправку
      if (!file.id) {
        fd.append(`resume[${index}]resume`, file, file.name)
      }
      index += 1
    })

    if (!isChanged) {
      setOpen(false)
      dispatch(
        enqueueSnackbar({
          info: {
            type: 'common',
            title: 'Профиль не изменился',
            message: 'Вы не внесли изменения в свои данные',
          },
          options: {
            key: new Date().getTime() + Math.random(),
            autoHideDuration: 6000,
          },
        }),
      )
      return
    }
    runPatch(onPatchProfile(fd))
  }

  const checkValidationError = () => {
    if (
      tabValue === 0 &&
      (errors?.contact_info?.message ||
        errors?.portfolio_url?.message ||
        errors?.resume?.message ||
        errors?.contact_type?.message)
    )
      return 'Допущена ошибка в блоке Резюме и портфолио'
    if (
      tabValue === 1 &&
      (errors?.image?.message ||
        errors?.city?.message ||
        errors.first_name?.message ||
        errors.last_name?.message ||
        errors?.gender?.message ||
        errors.birth_date?.message ||
        errors?.course?.message ||
        errors?.cohort_number?.message ||
        errors?.experience_type?.message ||
        errors.previous_job_position?.message ||
        errors?.education_type?.message ||
        errors.institution?.message)
    )
      return 'Допущена ошибка в блоке Информация профиля'
    return errors?.non_field_errors?.message
  }

  const validationError = checkValidationError()

  const submitHandler = handleSubmit(onSubmit)

  const firstTabValues = useMemo(
    () => ({
      control,
      avatar,
      errors,
      genderValue,
      date,
      courseValue,
      course,
      cohortValue,
      cohort,
      experienceValue,
      experience,
      degreeValue,
      education,
    }),
    [
      control,
      avatar,
      errors,
      genderValue,
      date,
      courseValue,
      course,
      cohortValue,
      cohort,
      experienceValue,
      experience,
      degreeValue,
      education,
    ],
  )

  const secondTabValues = useMemo(
    () => ({
      control,
      errors,
      methodValue,
      comMethods,
      addedFiles,
      deletedFiles,
      setIsFilesChanged,
    }),
    [control, errors, methodValue, comMethods, addedFiles, deletedFiles, setIsFilesChanged],
  )

  const firstTabHandlers = useMemo(
    () => ({
      handleImageChange,
      handleChangeGender,
      handleBirthdayChange,
      handleChangeCohort,
      handleChangeDegree,
      handleChangeExperience,
    }),
    [
      handleImageChange,
      handleChangeGender,
      handleBirthdayChange,
      handleChangeCohort,
      handleChangeDegree,
      handleChangeExperience,
    ],
  )

  const secondTabHandlers = useMemo(() => ({ handleMethodChange }), [handleMethodChange])
  return {
    handleClose,
    onSubmit: submitHandler,
    firstTabValues,
    secondTabValues,
    firstTabHandlers,
    secondTabHandlers,
    validationError,
  }
}

export default useEditProfile
