import React, { FC } from 'react'
import { Control, Controller } from 'react-hook-form'
import { FormHelperText, Typography } from '@mui/material/'
import MultiCitySearchBar from './MultiCitySearchBar/MultiCitySearchBar'
import useStyles from '../styles'

interface Props {
  control: Control<any>
  error: boolean
  helperText?: string
  width?: string | number
  hideLabel?: boolean
  disabled?: boolean
  limitTags?: number
}

const ControlledMultiCityField: FC<Props> = ({
  control,
  error,
  helperText,
  width,
  hideLabel = false,
  disabled = undefined,
  limitTags = undefined,
}) => {
  const classes = useStyles()
  return (
    <div className={classes.fieldContainer} style={{ width }}>
      {!hideLabel && <Typography variant="body2">Город</Typography>}
      <div>
        <Controller
          name="cities"
          control={control}
          render={({ field }) => (
            <MultiCitySearchBar
              value={field.value}
              onChange={field.onChange}
              error={error}
              disabled={disabled}
              limitTags={limitTags}
            />
          )}
        />
        <div className={classes.input}>
          <FormHelperText error>{helperText || ' '}</FormHelperText>
        </div>
      </div>
    </div>
  )
}

export default React.memo(ControlledMultiCityField)
