import React, { ChangeEvent, FC, ReactNode, useEffect, useMemo, useState } from 'react'
import debounce from 'lodash.debounce'
import TextField from '@mui/material/TextField'
import InputAdornment from '@mui/material/InputAdornment'
import Autocomplete from '@mui/material/Autocomplete'
import CircularProgress from '@mui/material/CircularProgress'
import { IconButton, Chip, AutocompleteValue } from '@mui/material'
import { AxiosError } from 'axios'
import useStyles from './MultiCitySearchBar.style'
import api from '../../../../../api/api'
import useAsync from '../../../../../hooks/useAsync'
import { API_LOCATION_DIRECTORY_ENDPOINT } from '../../../../../utils/constants'
import IconSearch from '../../../../../assets/Icons/IconSearch/IconSearch'
import IconTrash from '../../../../../assets/Icons/IconTrash/IconTrash'

const deleteIcon = (
  <IconButton sx={{ padding: '4px' }}>
    <IconTrash />
  </IconButton>
)
interface TValueItem {
  country: string
  id: number
  location: string
  location_type: string
}

interface Props {
  // onChange?: ((event: ChangeEvent<{ name?: string | undefined; value: unknown }>, child: ReactNode) => void) | undefined
  onChange: (tag: AutocompleteValue<unknown, undefined, undefined, undefined>) => void
  // value: Array<TValueItem>
  // В реальности value - это массив объектов, но код так написан, что периодически он используется не так
  // слишком долго разбираться
  value: any
  error: boolean
  disabled?: boolean
  limitTags?: number
}

const MultiCitySearchBar: FC<Props> = ({ onChange, value, error, disabled, limitTags = 2 }) => {
  const { run } = useAsync({
    data: [],
    error: null,
  })
  const [open, setOpen] = useState(false)
  const [inputValue, setInputValue] = useState<string | null>(null)
  const [options, setOptions] = useState([])
  const [loading, setLoading] = useState(false)
  const classes = useStyles()

  const getCities = async (query: string) => {
    try {
      setLoading(true)
      const response = await api.get(API_LOCATION_DIRECTORY_ENDPOINT, { params: { location: query } })
      const cities = response?.data
      setOptions(cities ?? [])
      setOpen(cities?.length > 0)

      return Promise.resolve(response?.data)
    } catch (e) {
      const err = e as AxiosError
      return Promise.reject(err.response)
    } finally {
      setLoading(false)
    }
  }

  const changeHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
    setInputValue(event?.target?.value)
  }

  const debouncedChangeHandler = useMemo(() => debounce(changeHandler, 300), [])

  useEffect(() => () => debouncedChangeHandler.cancel(), [])

  useEffect(() => {
    if (inputValue && inputValue?.length > 2) {
      run(getCities(inputValue))
    }
  }, [inputValue])

  useEffect(() => {
    if (!open) {
      setOptions([])
    }
  }, [open])

  // Убрал, потому что и без него работает

  // const handleDelete = (tagToDelete: any) => {
  //   /* eslint-disable-next-line @typescript-eslint/ban-ts-comment */
  //   // @ts-ignore
  //   onChange(value && value.filter((tag: any) => tag !== tagToDelete))
  // }

  return (
    <>
      <Autocomplete
        disabled={disabled}
        sx={{ width: 400, height: 40 }}
        clearText="Очистить"
        loadingText="Загрузка..."
        noOptionsText=""
        autoHighlight
        multiple
        open={open}
        className={classes.root}
        classes={{ paper: classes.paper }}
        onOpen={() => {
          setOpen(options.length > 0)
        }}
        onClose={() => {
          setOpen(false)
        }}
        isOptionEqualToValue={(option) => option.id === value.id}
        getOptionLabel={(option) => `${option?.location}, ${option?.country}`}
        options={options}
        loading={loading}
        value={value}
        /* eslint-disable-next-line @typescript-eslint/ban-ts-comment */
        // @ts-ignore
        onInputChange={debouncedChangeHandler}
        onChange={(_, data) => onChange(data)}
        limitTags={limitTags}
        renderTags={(values, getTagProps) =>
          values.map((option, index) => (
            <Chip
              sx={{
                height: '28px',
                backgroundColor: '#F1F6FF',
                borderRadius: '4px',
                fontFamily: ['YS Text', '-apple-system', 'BlinkMacSystemFont', 'Arial', 'Helvetica', 'sans-serif'].join(
                  ',',
                ),
                fontSize: 13,
                lineHeight: '16px',
                fontWeight: 400,
                color: '#1D6BF3',
              }}
              label={`${option?.location}, ${option?.country}`}
              deleteIcon={deleteIcon}
              {...getTagProps({ index })}
              // onDelete={handleDelete}
            />
          ))
        }
        renderInput={(params) => (
          <TextField
            {...params}
            error={error}
            placeholder="Поиск города"
            InputProps={{
              ...params.InputProps,
              startAdornment: (
                <>
                  {params.InputProps.startAdornment}
                  <InputAdornment position="start">
                    <IconSearch />
                  </InputAdornment>
                </>
              ),
              endAdornment: (
                <>
                  {loading ? <CircularProgress color="inherit" size={20} /> : null}
                  {params.InputProps.endAdornment}
                </>
              ),
            }}
          />
        )}
      />
      {/* <CityTags tags={value} onDelete={handleDelete} /> */}
    </>
  )
}

export default React.memo(MultiCitySearchBar)
