import React, { useState, useEffect, useMemo, useCallback } from 'react'
import isEqual from 'lodash.isequal'
import { format } from 'date-fns'
import { useForm, Controller } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormHelperText,
  IconButton,
  Stack,
  ThemeProvider,
  Tooltip,
  Typography,
} from '@mui/material'
import { DataGrid } from '@mui/x-data-grid'
import useStyles from './NewResponsePopup.style'
import { newResponseSchema } from '../../../../../utils/schemas'
import IconClose from '../../../../../assets/Icons/IconClose/IconClose'
import { API_RESPONSE_CONTACTS_ENDPOINT, COLORS, RESPONSE_COMMENT_MAX_SYMBOLS } from '../../../../../utils/constants'
import useAsync from '../../../../../hooks/useAsync'
import { formatApiDate } from '../../../../../utils/dates'
import theme from '../../../../../utils/themeMUI5'
import Tabs from '../../../../Sub/FormElements/custom-tabs/custom-tabs'
import Tab from '../../../../Sub/FormElements/custom-tab/custom-tab'
import TabPanel from '../../../../Sub/FormElements/tab-panel/tab-panel'
import {
  ControlledDateField,
  ControlledSelectField,
  ControlledTextField,
  ControlledMultiCityField,
} from '../../../../Sub/FormElements'
import CheckboxItem from '../../../../Sub/CheckboxGroup/CheckboxItem/CheckboxItem'
import contactColumns from './columns'
import api from '../../../../../api/api'
import LoadingOverlay from '../../../../Sub/DataTable/LoadingOverlay/LoadingOverlay'
import { useAppDispatch } from '../../../../../services/store/hooks'
import { enqueueSnackbar } from '../../../../../services/slices/app-slice'

const ColorBox = (props) => {
  const classes = useStyles(props)
  return <div className={classes.color} />
}

const colorBoxes = COLORS.map((item) => ({
  id: item.color,
  name: <ColorBox color={item.color} />,
}))

export default function NewResponsePopup({
  openState,
  onSubmit,
  data,
  onDeleteCard,
  isNewReport,
  currencies,
  readOnly,
  openNewContact,
  closeResponsesPopup,
  contactsForNewResponseState,
  setContactToEdit,
}) {
  const dispatch = useAppDispatch()
  const [open, setOpen] = openState
  const [contacts, setContacts] = useState([])
  const [newResponseContacts, setNewResponseContacts] = contactsForNewResponseState || []
  const [tabValue, setTabValue] = useState(0)
  const [validationSchema, setValidationSchema] = useState(newResponseSchema)
  const {
    control,
    handleSubmit,
    register,
    setValue,
    trigger,
    watch,
    reset,
    getValues,
    setError,
    clearErrors,
    formState: { errors },
  } = useForm({
    defaultValues: useMemo(() => data, [data]),
    resolver: useMemo(() => yupResolver(validationSchema), [validationSchema]),
    mode: 'onChange',
  })
  const { run } = useAsync({
    data: null,
    error: null,
  })

  const isRemoteWorkChecked = watch('is_remote_work')

  useEffect(() => {
    if (isRemoteWorkChecked) {
      setValue('cities', [], { shouldValidate: true, shouldDirty: true })
    }
  }, [isRemoteWorkChecked])

  const isDateLaterThenTwoWeeks = (value) => {
    const dateToCheck = new Date(value)
    dateToCheck.setHours(0, 0, 0, 0)
    const twoWeeksAgo = new Date()
    twoWeeksAgo.setHours(0, 0, 0, 0)
    twoWeeksAgo.setDate(twoWeeksAgo.getDate() - 13)
    return dateToCheck < twoWeeksAgo
  }

  useEffect(() => {
    reset(data)
    const fieldsToRemoveFromValidation = []
    if (data?.response_date) {
      const isLater = isDateLaterThenTwoWeeks(data?.response_date)
      if (isLater) fieldsToRemoveFromValidation.push('response_date')
    }
    setValidationSchema(newResponseSchema.omit(fieldsToRemoveFromValidation))
    // Выставляем значение таба только при смене данных о карточке
    setTabValue(0)
  }, [data])

  const [showDeletePopup, setDeletePopup] = useState(false)
  const openDeletePopup = () => {
    setDeletePopup(true)
  }
  const closeDeletePopup = () => {
    setDeletePopup(false)
  }

  const [responseDate, setResponseDate] = useState(Date.now())
  const [hrInterviewDate, setHrInterviewDate] = useState(null)
  const [testTaskDate, setTestTaskDate] = useState(null)
  const [techInterviewDate, setTechInterviewDate] = useState(null)
  const [bossInterviewDate, setBossInterviewDate] = useState(null)
  const [offerDate, setOfferDate] = useState(null)
  const [rejectionDate, setRejectionDate] = useState(null)

  const clearDateErrors = () => {
    clearErrors('response_date')
    clearErrors('hr_interview_date')
    clearErrors('test_task_date')
    clearErrors('tech_interview_date')
    clearErrors('boss_interview_date')
    clearErrors('offer_date')
    clearErrors('rejection_date')
  }

  const resetDayPickers = () => {
    const today = Date.now()
    setResponseDate(today)
    setValue('response_date', format(today, 'yyyy-MM-dd'), {
      shouldValidate: true,
      shouldDirty: true,
    })
    setHrInterviewDate(null)
    setTestTaskDate(null)
    setTechInterviewDate(null)
    setBossInterviewDate(null)
    setOfferDate(null)
    setRejectionDate(null)

    clearDateErrors()
  }

  const resetForm = () => {
    reset({})
  }

  const colorValue = watch('color_id')
  const currencyValue = watch('currency')
  const commentText = watch('comment')

  useEffect(() => {
    register('color_id')
    register('currency')
    register('comment')
  }, [register])

  useEffect(() => {
    function getRandomArbitrary(min = 0, max) {
      return Math.round(Math.random() * (max - min) + min)
    }
    if (!data) {
      if (isNewReport) {
        if (!contacts?.length && !newResponseContacts?.length && open) {
          resetForm()
          resetDayPickers()
        }
        const randomColorIndex = getRandomArbitrary(0, COLORS.length - 1)
        setValue('color_id', COLORS[randomColorIndex].color)
        setValue('currency', 4)
      }
    } else {
      const responseDateObj = new Date(data.response_date)
      setResponseDate(responseDateObj)

      const hrInterviewDateObj = data.hr_interview_date ? new Date(data.hr_interview_date) : null
      setHrInterviewDate(hrInterviewDateObj)

      const testTaskDateObj = data.test_task_date ? new Date(data.test_task_date) : null
      setTestTaskDate(testTaskDateObj)

      const techInterviewDateObj = data.tech_interview_date ? new Date(data.tech_interview_date) : null
      setTechInterviewDate(techInterviewDateObj)

      const bossInterviewDateObj = data.boss_interview_date ? new Date(data.boss_interview_date) : null
      setBossInterviewDate(bossInterviewDateObj)

      const offerDateDateObj = data.offer_date ? new Date(data.offer_date) : data.offer_date
      setOfferDate(offerDateDateObj)

      const rejectionDateObj = data.rejection_date ? new Date(data.rejection_date) : null
      setRejectionDate(rejectionDateObj)

      clearDateErrors()
    }
    /*
    if (!contacts?.length && !newResponseContacts?.length) {
      setTabValue(0)
    } */
  }, [open])

  const handleChangecolor = (event) => {
    setValue('color_id', event.target.value)
  }

  const classes = useStyles()

  const handleClose = () => {
    closeResponsesPopup()
    if (!readOnly) setNewResponseContacts([])
    setContacts([])
    resetForm()
    resetDayPickers()
  }

  const onSubmitVacancy = async (vacancyData, message) => {
    try {
      const response = await onSubmit(vacancyData)
      dispatch(
        enqueueSnackbar({
          info: {
            type: 'common',
            ...message,
          },
          options: {
            key: new Date().getTime() + Math.random(),
            autoHideDuration: 6000,
          },
        }),
      )
      return Promise.resolve(response)
    } catch (e) {
      if (typeof e !== 'object') {
        setError('non_field_errors', {
          type: 'server',
          message: 'Серверная ошибка',
        })
      } else {
        const serverErrors = e
        Object.keys(serverErrors).forEach((field) => {
          let errorMessage = ''
          if (Array.isArray(serverErrors[field])) {
            errorMessage = serverErrors[field].join(', ')
          } else {
            errorMessage = serverErrors[field]
          }
          setError(field, {
            type: 'server',
            message: errorMessage,
          })
        })
      }
      return Promise.reject(e)
    }
  }

  function submitHandler(formData) {
    let requestData = {}
    let message = {}
    let isDataUpdated = false
    const formatDate = (date) => (date ? formatApiDate(date) : null)
    const isFieldChanged = (fieldName) => {
      const isChanged = data && !isEqual(data[fieldName], formData[fieldName])
      if (isChanged) isDataUpdated = true
      return isChanged
    }
    const isDateChanged = (fieldName, value) => {
      const formattedValue = value ? format(value, 'yyyy-MM-dd') : null
      const isChanged = data && data[fieldName] !== formattedValue
      if (isChanged) isDataUpdated = true
      return isChanged
    }

    const formattedResponseDate = formatDate(responseDate)
    const formattedInterviewDate = formatDate(hrInterviewDate)
    const formattedTestTaskDate = formatDate(testTaskDate)
    const formattedTechInterviewDate = formatDate(techInterviewDate)
    const formattedBossInterviewDate = formatDate(bossInterviewDate)
    const formattedOfferDate = formatDate(offerDate)
    const formattedRejectionDate = formatDate(rejectionDate)

    const cities = formData.cities?.map((city) => city.id)

    if (!data) {
      let salary = {}
      if (formData?.salary && (!formData?.salary_max || formData?.salary_max === 0)) {
        salary = {
          salary: formData?.salary,
          salary_max: null,
          currency: formData?.currency,
        }
      } else if ((!formData?.salary || formData?.salary === 0) && formData?.salary_max) {
        salary = {
          salary: null,
          salary_max: formData?.salary_max,
          currency: formData?.currency,
        }
      } else if (formData?.salary && formData?.salary_max) {
        salary = {
          salary: formData?.salary,
          salary_max: formData?.salary_max,
          currency: formData?.currency,
        }
      }

      requestData = {
        ...salary,
        company_name: formData.company_name,
        position: formData.position,
        cities: formData.is_remote_work ? [] : cities,
        is_remote_work: !!formData.is_remote_work,
        color_id: formData.color_id,
        comment: formData.comment || undefined,
        url: formData.url || undefined,
        response_date: formattedResponseDate,
        hr_interview_date: formattedInterviewDate || undefined,
        test_task_date: formattedTestTaskDate || undefined,
        tech_interview_date: formattedTechInterviewDate || undefined,
        boss_interview_date: formattedBossInterviewDate || undefined,
        offer_date: formattedOfferDate || undefined,
        rejection_date: formattedRejectionDate || undefined,
      }
      message = {
        open: true,
        title: 'Отклик добавлен',
        message: 'Данные успешно сохранены',
      }
    } else {
      requestData = {
        company_name: isFieldChanged('company_name') ? formData.company_name : undefined,
        position: isFieldChanged('position') ? formData.position : undefined,
        cities:
          // eslint-disable-next-line no-nested-ternary
          formData.is_remote_work && isFieldChanged('cities') ? [] : isFieldChanged('cities') ? cities : undefined,
        is_remote_work: isFieldChanged('is_remote_work') ? !!formData.is_remote_work : undefined,
        // eslint-disable-next-line no-nested-ternary
        salary: !isFieldChanged('salary') ? undefined : formData.salary === 0 ? null : formData.salary,
        // eslint-disable-next-line no-nested-ternary
        salary_max: !isFieldChanged('salary_max') ? undefined : formData.salary_max === 0 ? null : formData.salary_max,
        currency: isFieldChanged('currency') ? formData.currency : undefined,
        color_id: isFieldChanged('color_id') ? formData.color_id : undefined,
        comment: isFieldChanged('comment') ? formData.comment : undefined,
        url: isFieldChanged('url') ? formData.url : undefined,
        response_date: isDateChanged('response_date', responseDate) ? formattedResponseDate : undefined,
        hr_interview_date: isDateChanged('hr_interview_date', hrInterviewDate) ? formattedInterviewDate : undefined,
        test_task_date: isDateChanged('test_task_date', testTaskDate) ? formattedTestTaskDate : undefined,
        tech_interview_date: isDateChanged('tech_interview_date', techInterviewDate)
          ? formattedTechInterviewDate
          : undefined,
        boss_interview_date: isDateChanged('boss_interview_date', bossInterviewDate)
          ? formattedBossInterviewDate
          : undefined,
        offer_date: isDateChanged('offer_date', offerDate) ? formattedOfferDate : undefined,
        rejection_date: isDateChanged('rejection_date', rejectionDate) ? formattedRejectionDate : undefined,
      }
      message = {
        open: true,
        title: 'Отклик обновлён',
        message: 'Данные успешно сохранены',
      }
    }

    requestData.last_update_datetime = formatApiDate(new Date())

    if (data && !isDataUpdated) {
      handleClose()
      dispatch(
        enqueueSnackbar({
          info: {
            type: 'common',
            title: 'Отклик не изменился',
            message: 'Вы не внесли изменений в данные',
          },
          options: {
            key: new Date().getTime() + Math.random(),
            autoHideDuration: 6000,
          },
        }),
      )
      return
    }

    run(onSubmitVacancy(requestData, message))
  }

  const handleDelete = () => {
    onDeleteCard()
    setOpen(false)
    closeDeletePopup()
  }

  const handleTabChange = useCallback((event, newValue) => {
    setTabValue(newValue)
  }, [])

  const handleCurrenciesChange = async (e) => {
    setValue('currency', e.target.value, { shouldValidate: true, shouldDirty: true })
  }

  const handleSalaryChange = async (e) => {
    setValue('salary', e.target.value, { shouldValidate: true, shouldDirty: true })
    await trigger('salary_max')
    return !currencyValue ? setValue('currency', 4, { shouldValidate: true, shouldDirty: true }) : null
  }

  const handleSalaryMaxChange = async (e) => {
    setValue('salary_max', e.target.value, { shouldValidate: true, shouldDirty: true })
    return !currencyValue ? setValue('currency', 4, { shouldValidate: true, shouldDirty: true }) : null
  }

  const handleDateChange = (val, fieldName, setDateState) => {
    if (val) {
      setValue(fieldName, format(val, 'yyyy-MM-dd'), {
        shouldValidate: true,
        shouldDirty: true,
      })
      setDateState(val)
    } else {
      setValue(fieldName, null, {
        shouldValidate: true,
        shouldDirty: true,
      })
      setDateState(null)
    }
  }

  const checkValidationError = () => {
    if (tabValue === 0 && errors?.comment?.message) return 'Допущена ошибка в блоке Заметки'
    if (
      tabValue === 1 &&
      (errors?.company_name?.message ||
        errors?.position?.message ||
        errors?.url?.message ||
        errors?.cities?.message ||
        errors?.is_remote_work?.message ||
        errors?.salary?.message ||
        errors?.salary_max?.message ||
        errors?.currency?.message ||
        errors?.color_id?.message)
    )
      return 'Допущена ошибка в блоке Общая информация'
    return errors?.non_field_errors?.message
  }

  const validationError = checkValidationError()

  const { run: runContacts, isLoading } = useAsync({
    data: [],
    error: null,
  })

  const getContacts = async () => {
    try {
      const response = await api.get(API_RESPONSE_CONTACTS_ENDPOINT(data?.id))
      setContacts(response?.data?.results)
      return Promise.resolve(response.data)
    } catch (e) {
      return Promise.reject(e.response)
    }
  }

  useEffect(() => {
    if (open && data?.id && !isNewReport && !readOnly) {
      runContacts(getContacts())
    }
  }, [open])

  const handleOpenNewContactPopup = (e) => {
    e.preventDefault()
    openNewContact()
  }

  const onCellClick = (params, event) => {
    // eslint-disable-next-line no-param-reassign
    event.defaultMuiPrevented = true
    setContactToEdit(params.row)
    openNewContact()
  }

  if (showDeletePopup)
    return (
      <ThemeProvider theme={theme}>
        <Dialog
          variant="confirm"
          open={showDeletePopup}
          onClose={closeDeletePopup}
          sx={{
            '&.MuiDialog-paper': {
              width: 386,
              minWidth: 386,
              maxWidth: 386,
              height: 210,
              minHeight: 210,
              padding: 40,
              overflow: 'hidden',
            },
          }}
        >
          <DialogTitle>Удаление отклика</DialogTitle>

          <DialogContent sx={{ alignItems: 'start', marginTop: '5px' }}>Удалить этот отклик из дневника?</DialogContent>

          <div className={classes.buttonContainer}>
            <Button
              color="secondary"
              sx={{ minHeight: '50px', maxHeight: '50px' }}
              type="submit"
              onClick={closeDeletePopup}
            >
              Оставить
            </Button>
            <Button color="primary" sx={{ minHeight: '50px', maxHeight: '50px' }} type="submit" onClick={handleDelete}>
              Удалить
            </Button>
          </div>
        </Dialog>
      </ThemeProvider>
    )

  return (
    <ThemeProvider theme={theme}>
      <Dialog
        variant="response"
        open={open}
        onClose={handleClose}
        sx={{
          '& .MuiDialog-paper': {
            minWidth: 800,
            maxWidth: 996,
            height: 636,
            minHeight: 636,
            padding: 0,
            display: 'grid',
            gridTemplateColumns: '712px 284px',
            gridTemplateRows: tabValue !== 2 ? '120px 1fr 112px' : '120px 1fr',
          },
        }}
      >
        <DialogTitle
          variant="response"
          component="div"
          sx={{
            '&.MuiDialogTitle-root': {
              gridColumn: 1,
              gridRow: 1,
              padding: '40px 40px 20px',
              display: 'flex',
              justifyContent: 'space-between',
            },
          }}
        >
          <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'start' }}>
            <Typography
              variant="h2"
              id="form-dialog-title"
              sx={{
                marginBottom: '4px',
                maxWidth: 400,
                whiteSpace: 'nowrap',
                textOverflow: 'ellipsis',
                overflow: 'hidden',
              }}
            >
              {watch('company_name') ? getValues('company_name') : 'Название компании'}
            </Typography>
            <Typography
              variant="body3"
              sx={{
                maxWidth: 400,
                whiteSpace: 'nowrap',
                textOverflow: 'ellipsis',
                overflow: 'hidden',
                color: '#797981',
              }}
            >
              {watch('position') ? getValues('position') : 'Должность'}
            </Typography>
          </Box>
          {data && !readOnly && (
            <Button variant="delete" sx={{ width: 144 }} onClick={openDeletePopup}>
              Удалить отклик
            </Button>
          )}
        </DialogTitle>
        <DialogContent
          variant="response"
          sx={{
            height: tabValue === 2 ? 481 : undefined,
            '&.MuiDialogContent-root': {
              gridColumn: 1,
              gridRow: 2,
              alignItems: 'inherit',
              padding: '13px 40px 20px',
            },
          }}
        >
          <Tabs
            value={tabValue}
            onChange={handleTabChange}
            sx={{
              minHeight: 32,
              '& .MuiTabs-flexContainer': {
                gap: '20px',
              },
              '&.MuiTab-root': {
                fontFamily: ['YS Text', '-apple-system', 'BlinkMacSystemFont', 'Arial', 'Helvetica', 'sans-serif'].join(
                  ',',
                ),
                fontSize: 18,
              },
            }}
          >
            <Tab label="Общая информация" variant="response" />
            <Tab label="Заметки" variant="response" />
            {!readOnly && <Tab label="Контакты" variant="response" />}
          </Tabs>
          <form onSubmit={handleSubmit(submitHandler)} id="form">
            <TabPanel
              value={tabValue}
              index={0}
              style={{
                display: 'grid',
                gridTemplateColumns: '1fr 1fr',
                gridColumnGap: 20,
                gridRowGap: 4,
                padding: '37px 0',
              }}
            >
              <ControlledTextField
                labelTitle="Название компании"
                placeholder="Введи название компании"
                fieldName="company_name"
                control={control}
                error={!!errors.company_name?.message}
                helperText={errors.company_name?.message}
                disabled={readOnly}
              />
              <ControlledTextField
                labelTitle="Должность"
                placeholder="Введи название должности"
                fieldName="position"
                control={control}
                error={!!errors.position?.message}
                helperText={errors.position?.message}
                disabled={readOnly}
              />
              <div style={{ gridColumn: '1 / 3', display: 'flex', gap: 20 }}>
                <ControlledMultiCityField
                  control={control}
                  error={!!errors?.cities?.message}
                  helperText={errors?.cities?.message}
                  disabled={readOnly || isRemoteWorkChecked}
                  width={408}
                  limitTags={1}
                />
                <Controller
                  name="is_remote_work"
                  control={control}
                  render={({ field }) => (
                    <CheckboxItem
                      {...field}
                      checkboxLabel="Удаленная работа"
                      disabled={readOnly}
                      checked={isRemoteWorkChecked}
                      margin={{ marginBottom: 0 }}
                    />
                  )}
                />
              </div>
              <Box className={classes.salaryContainer}>
                <Typography variant="body2" className={classes.salaryLabel}>
                  Зарплата
                </Typography>
                <Box className={classes.salaryInputContainer}>
                  <ControlledTextField
                    fieldName="salary"
                    control={control}
                    // value={salary}
                    error={!!errors.salary?.message}
                    helperText={errors.salary?.message}
                    placeholder="От"
                    onChange={handleSalaryChange}
                    maxWidth={148}
                    disabled={readOnly}
                  />
                  <ControlledTextField
                    fieldName="salary_max"
                    control={control}
                    // value={salaryMax}
                    error={!!errors.salary_max?.message}
                    helperText={errors.salary_max?.message}
                    placeholder="До"
                    onChange={handleSalaryMaxChange}
                    maxWidth={148}
                    disabled={readOnly}
                  />
                </Box>
              </Box>
              <Box className={classes.twoFieldInRowContainer}>
                <ControlledSelectField
                  labelTitle="Валюта"
                  tipContent="Выбери валюту"
                  value={+currencyValue}
                  error={!!errors?.currency?.message}
                  helperText={errors?.currency?.message}
                  onChange={handleCurrenciesChange}
                  items={currencies}
                  maxWidth="148px"
                  disabled={readOnly}
                />
                <ControlledSelectField
                  labelTitle="Цвет"
                  tipContent="Выбери цвет"
                  value={colorValue}
                  error={!!errors?.color_id?.message}
                  helperText={errors?.color_id?.message}
                  onChange={handleChangecolor}
                  items={colorBoxes}
                  maxWidth="148px"
                  disabled={readOnly}
                  isColor
                />
              </Box>
              <ControlledTextField
                labelTitle="URL"
                placeholder="Укажи сайт компании"
                fieldName="url"
                control={control}
                error={!!errors.url?.message}
                helperText={errors.url?.message}
                disabled={readOnly}
              />
            </TabPanel>
            <TabPanel value={tabValue} index={1} style={{ marginTop: -54 }}>
              <ControlledTextField
                fieldName="comment"
                control={control}
                error={!!errors.comment?.message}
                helperText={errors.comment?.message}
                placeholder="Здесь ты можешь добавить дополнительную информацию о компании, условия работы и заметки с собеседований"
                multiline
                rows={14}
                symbolsCount={commentText?.length ?? 0}
                maxLength={RESPONSE_COMMENT_MAX_SYMBOLS}
                maxWidth={632}
                sx={{
                  '& .MuiInputBase-input': {
                    paddingRight: '12px !important',
                  },
                }}
                disabled={readOnly}
              />
            </TabPanel>
            {!readOnly && (
              <TabPanel
                value={tabValue}
                index={2}
                style={{
                  marginTop: '-54px',
                  display: 'flex',
                  flexDirection: 'column',
                }}
              >
                {contacts?.length || (newResponseContacts?.length && isNewReport) ? (
                  <Box className={classes.contactsBox}>
                    <Box className={classes.contactsInfoBox}>
                      <Typography variant="body3">
                        Всего контактов: {isNewReport ? newResponseContacts?.length : contacts.length}
                      </Typography>
                      <Tooltip
                        placement="top-start"
                        classes={{ tooltip: classes.tooltip }}
                        enterDelay={100}
                        leaveDelay={200000}
                        title={
                          contacts.length >= 10 || newResponseContacts.length >= 10
                            ? 'Ты достиг максимального кол-ва контактов для компании - 10'
                            : ''
                        }
                      >
                        <span>
                          <Button
                            disabled={contacts.length >= 10 || newResponseContacts.length >= 10}
                            type="submit"
                            sx={{ width: '160px' }}
                            className={classes.contactButton}
                            onClick={handleOpenNewContactPopup}
                          >
                            Добавить контакт
                          </Button>
                        </span>
                      </Tooltip>
                    </Box>
                    <Box
                      sx={{
                        height: 347,
                        width: '100%',
                        marginTop: '14px',
                      }}
                    >
                      <DataGrid
                        onCellClick={onCellClick}
                        rows={!isNewReport ? contacts : newResponseContacts}
                        columns={contactColumns}
                        pageSize={100}
                        sx={{
                          minHeight: 291,
                          '&.MuiDataGrid-cellContent': {},
                          '& .MuiDataGrid-cell': { cursor: 'pointer' },
                        }}
                        rowsPerPageOptions={[100]}
                        disableSelectionOnClick
                        disableColumnMenu
                        disableColumnSelector
                        rowHeight={63}
                        isLoading={isLoading}
                        components={{
                          NoRowsOverlay: () => (
                            <Stack height="100%" alignItems="center" justifyContent="center">
                              Нет данных
                            </Stack>
                          ),
                          NoResultsOverlay: () => (
                            <Stack height="100%" alignItems="center" justifyContent="center">
                              Local filter returns no result
                            </Stack>
                          ),
                          ErrorOverlay: () => (
                            <Stack height="100%" alignItems="center" justifyContent="center">
                              Error
                            </Stack>
                          ),
                          LoadingOverlay,
                        }}
                      />
                    </Box>
                  </Box>
                ) : (
                  <Box className={classes.emptyTableBox}>
                    <Typography variant="body4">У тебя не создано ещё ни одного контакта</Typography>
                    <Button
                      type="submit"
                      sx={{ width: '250px', minHeight: '50px' }}
                      onClick={handleOpenNewContactPopup}
                    >
                      Добавить контакт
                    </Button>
                  </Box>
                )}
              </TabPanel>
            )}
          </form>
        </DialogContent>
        {tabValue !== 2 && (
          <DialogActions
            variant="response"
            sx={{
              '&.MuiDialogActions-root': {
                gridColumn: 1,
                gridRow: 3,
                padding: '0 40px 40px',
              },
            }}
          >
            <FormHelperText error style={{ textAlign: 'center', margin: '5px 0' }}>
              {validationError || ' '}
            </FormHelperText>
            {readOnly ? (
              <Button type="submit" sx={{ width: 285, minHeight: 50, maxHeight: 50 }} onClick={() => setOpen(false)}>
                Закрыть
              </Button>
            ) : (
              <Button form="form" type="submit" sx={{ width: 285, minHeight: 50, maxHeight: 50 }}>
                {data ? 'Сохранить' : 'Отправить'}
              </Button>
            )}
          </DialogActions>
        )}
        <Stack
          // variant="response"
          spacing="6px"
          sx={{
            backgroundColor: '#f1f6ff',
            gridColumn: 2,
            gridRow: '1 / 4',
            padding: '40px',
            boxSizing: 'border-box',
            position: 'relative',
          }}
        >
          <IconButton onClick={handleClose} variant="close-popup">
            <IconClose />
          </IconButton>
          <ControlledDateField
            labelTitle="Отклик на вакансию"
            fieldName="response_date"
            dateValue={responseDate}
            error={!!errors.response_date?.message}
            helperText={errors.response_date?.message}
            onChange={(val) => handleDateChange(val, 'response_date', setResponseDate)}
            type="standard"
            disabled={readOnly}
            clearable={false}
          />
          <ControlledDateField
            labelTitle="Собеседование с HR"
            fieldName="hr_interview_date"
            dateValue={hrInterviewDate}
            error={!!errors.hr_interview_date?.message}
            helperText={errors.hr_interview_date?.message}
            onChange={(val) => handleDateChange(val, 'hr_interview_date', setHrInterviewDate)}
            type="standard"
            disabled={readOnly}
          />
          <ControlledDateField
            labelTitle="Тестовое задание"
            fieldName="test_task_date"
            dateValue={testTaskDate}
            error={!!errors.test_task_date?.message}
            helperText={errors.test_task_date?.message}
            onChange={(val) => handleDateChange(val, 'test_task_date', setTestTaskDate)}
            type="standard"
            disabled={readOnly}
          />
          <ControlledDateField
            labelTitle="Техническое собеседование"
            fieldName="tech_interview_date"
            dateValue={techInterviewDate}
            error={!!errors.tech_interview_date?.message}
            helperText={errors.tech_interview_date?.message}
            onChange={(val) => handleDateChange(val, 'tech_interview_date', setTechInterviewDate)}
            type="standard"
            disabled={readOnly}
          />
          <ControlledDateField
            labelTitle="Собеседование с руководителем"
            fieldName="tech_interview_date"
            dateValue={bossInterviewDate}
            error={!!errors.boss_interview_date?.message}
            helperText={errors.boss_interview_date?.message}
            onChange={(val) => handleDateChange(val, 'boss_interview_date', setBossInterviewDate)}
            type="standard"
            disabled={readOnly}
          />
          <ControlledDateField
            labelTitle="Оффер"
            fieldName="offer_date"
            dateValue={offerDate}
            error={!!errors.offer_date?.message}
            helperText={errors.offer_date?.message}
            onChange={(val) => handleDateChange(val, 'offer_date', setOfferDate)}
            type="standard"
            disabled={readOnly}
          />
          <ControlledDateField
            labelTitle="Отказ"
            fieldName="rejection_date"
            dateValue={rejectionDate}
            error={!!errors.rejection_date?.message}
            helperText={errors.rejection_date?.message}
            onChange={(val) => handleDateChange(val, 'rejection_date', setRejectionDate)}
            type="standard"
            disabled={readOnly}
          />
        </Stack>
      </Dialog>
    </ThemeProvider>
  )
}
