import React from 'react'
import { Card, CardContent, CardHeader, IconButton, Typography } from '@material-ui/core'
import { isYesterday, isToday, format } from 'date-fns'
import { ru } from 'date-fns/locale'
import { useDrag } from 'react-dnd'
import IconClip from '../../../../../assets/Icons/IconClip/IconClip'

import useStyles from './CompanyCard.styles'
import checkAbsoluteLink from '../../../../../utils/checkAbsoluteLink'
import IconPartner from '../../../../../assets/Icons/IconPartner/IconPartner'

const CompanyCard = ({ card, handleSetCardData, handleOpenPopup }) => {
  const classes = useStyles()
  const [{ isDrag }, dragRef] = useDrag({
    type: 'card',
    item: { id: card?.id, status_id: card?.status_id, response_date: card?.response_date },
    collect: (monitor) => ({
      isDrag: monitor.isDragging(),
    }),
  })
  const getActualDate = () => {
    let date = null
    if (card?.status_id === 'response') {
      date = card?.response_date
    }
    if (card?.status_id === 'hr_interview') {
      date = card?.hr_interview_date
    }
    if (card?.status_id === 'test_task') {
      date = card?.test_task_date
    }
    if (card?.status_id === 'tech_interview') {
      date = card?.tech_interview_date
    }
    if (card?.status_id === 'boss_interview') {
      date = card?.boss_interview_date
    }
    if (card?.status_id === 'offer') {
      date = card?.offer_date
    }
    if (card?.status_id === 'rejection') {
      date = card?.rejection_date
    }
    if (date === null) {
      return date
    }
    date = Date.parse(date)

    if (isToday(date)) {
      return 'Сегодня'
    }
    if (isYesterday(date)) {
      return 'Вчера'
    }

    return format(date, 'dd MMM', { locale: ru })
  }
  const handleCardClick = () => {
    handleOpenPopup()
    handleSetCardData(card)
  }

  let cardClassName = card ? classes.root : `${classes.root} ${classes.outline}`
  cardClassName = isDrag ? `${cardClassName} ${classes.drag}` : cardClassName

  return (
    <Card className={cardClassName} style={{ backgroundColor: card?.color_id }} onClick={handleCardClick}>
      {' '}
      {/* ref={dragRef} */}
      <CardHeader
        title={<Typography className={classes.title}>{card?.company_name}</Typography>}
        subheader={<Typography className={classes.subTitle}>{card?.position}</Typography>}
        action={
          card?.url ? (
            <IconButton href={checkAbsoluteLink(card?.url)} target="_blank" onClick={(e) => e.stopPropagation()}>
              <IconClip />
            </IconButton>
          ) : undefined
        }
        className={classes.header}
      />
      {card?.is_partner_vacancy && <IconPartner className={classes.partnerIcon} />}
      <CardContent className={classes.date}>{getActualDate()}</CardContent>
    </Card>
  )
}

export default CompanyCard
