import React, { FC } from 'react'
import { InputAdornment, IconButton, Typography } from '@material-ui/core'
import { KeyboardDatePicker, MuiPickersUtilsProvider } from '@material-ui/pickers'
import DateFnsUtils from '@date-io/date-fns'
import ruLocale from 'date-fns/locale/ru'
import IconCalendar from '../../../../assets/Icons/IconCalendar/IconCalendar'
import useStyles from '../styles'
import DatePicker from '../../CustomDatePicker/CustomDatePicker'

type Props = {
  labelTitle?: string
  dateValue?: string | number | null | Date
  helperText?: string
  type?: 'keyboard' | 'standard'
  fieldName: string
  disabled?: boolean
  error?: boolean
  onChange?: (dateValue: string) => void
}

const ControlledDateField: FC<Props> = ({
  labelTitle,
  dateValue,
  helperText,
  type = 'keyboard',
  fieldName,
  disabled,
  ...props
}) => {
  const classes = useStyles()
  return (
    <div className={classes.fieldContainer}>
      <Typography variant="body2">{labelTitle}</Typography>
      {type === 'keyboard' ? (
        <MuiPickersUtilsProvider utils={DateFnsUtils} locale={ruLocale}>
          <KeyboardDatePicker
            /* Этот компонент порсит обязательный пропс. Вроде ошибку при наборе не выбивает */
            onChange={function (date: any, value?: string | null | undefined): void {
              throw new Error('Function not implemented.')
            }}
            keyboardIcon={
              <IconButton>
                <IconCalendar isGray={!dateValue} />
              </IconButton>
            }
            InputAdornmentProps={{
              position: 'start',
              variant: 'standard',
            }}
            DialogProps={{ disableScrollLock: true }}
            value={dateValue}
            helperText={helperText || ' '}
            className={classes.picker}
            placeholder="Выбери дату"
            format="dd.MM.yyyy"
            inputVariant="outlined"
            cancelLabel="Отмена"
            {...props}
          />
        </MuiPickersUtilsProvider>
      ) : (
        <DatePicker
          InputProps={{
            startAdornment: (
              <InputAdornment position="end">
                <IconButton disabled={disabled}>
                  <IconCalendar isGray={!dateValue} />
                </IconButton>
              </InputAdornment>
            ),
          }}
          helperText={helperText || ' '}
          placeholder={!disabled && 'Выбери дату'}
          DialogProps={{ disableScrollLock: true }}
          clearLabel="Удалить дату"
          cancelLabel="Отменить"
          okLabel="Сохранить"
          clearable
          inputVariant="outlined"
          format="dd MMM yyyy"
          disableToolbar
          variant="dialog"
          value={dateValue}
          {...props}
        />
      )}
    </div>
  )
}

export default React.memo(ControlledDateField)
