const processServerNewContactError = (error, setError) => {
  if (typeof error?.response?.data !== 'object') {
    setError('non_field_errors', {
      type: 'server',
      message: 'Серверная ошибка',
    })
  } else {
    const serverErrors = error?.response.data
    Object.keys(serverErrors).forEach((field) => {
      // Обработчик на случай, если бэк вернёт ошибку целиком на поле social_network
      if (field === 'social_networks' && typeof serverErrors[field][0] === 'string') {
        // TODO переделать когда будет больше одной соцсети
        setError('contact_info', {
          type: 'server',
          message: serverErrors[field][0],
        })
      }
      if (field === 'social_networks') {
        // TODO переделать когда будет больше одной соцсети
        Object.keys(serverErrors[field][0]).forEach((networkField) => {
          setError(networkField, {
            type: 'server',
            message: serverErrors[field][0][networkField].toString(),
          })
        })
      }
      if (field === 'vacancy') {
        setError('responses_field', {
          type: 'server',
          message: serverErrors[field].toString(),
        })
      } else {
        setError(field, {
          type: 'server',
          message: serverErrors[field].toString(),
        })
      }
    })
  }
  return Promise.reject(error?.message)
}

export default processServerNewContactError
