import React from 'react'
import { Controller } from 'react-hook-form'
import { FormHelperText, Typography } from '@mui/material/'
import useStyles from '../../../FormElements/styles'
import CompanySearchBarFoundJob from '../CompanySearchBarFoundJob/CompanySearchBarFoundJob'

const ControlledCompanyFieldFoundJob = ({
  control,
  error,
  helperText,
  width,
  hideLabel,
  disabled,
  height,
  onCompanyChange,
  endpoint,
}) => {
  const classes = useStyles()

  return (
    <div className={classes.fieldContainer} style={{ width, maxHeight: height || 75, height }}>
      {!hideLabel && <Typography variant="body2">Название компании</Typography>}
      <Controller
        name="company_name"
        control={control}
        render={({ field }) => (
          <CompanySearchBarFoundJob
            value={field?.value}
            error={error}
            disabled={disabled}
            onChange={onCompanyChange}
            endpoint={endpoint}
          />
        )}
      />
      <div className={classes.input} style={{ height: 12, marginTop: '-1px' }}>
        <FormHelperText className="Mui-error">{helperText || ' '}</FormHelperText>
      </div>
    </div>
  )
}

export default React.memo(ControlledCompanyFieldFoundJob)
