import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import { Button, Typography } from '@material-ui/core'
import DialogContent from '@material-ui/core/DialogContent'
import useStyles from './ExitPopup.style'
import {
  EXIT_POPUP_TITLE,
  EXIT_POPUP_SUBTITLE,
  EXIT_POPUP_CLOSE_BUTTON,
  EXIT_POPUP_EXIT_BUTTON,
} from '../../../../utils/constants'

export default function ExitPopup({ open, handleClose, handleCloseExit }) {
  const classes = useStyles()
  return (
    <div>
      <Dialog
        maxWidth="xl"
        classes={{ paper: classes.root }}
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <div className={classes.container}>
          <DialogContent className={classes.dialogContent}>
            <Typography className={classes.popupHeader} component="h2" variant="h2">
              {EXIT_POPUP_TITLE}
            </Typography>
            <Typography className={classes.popupDescription} component="p" varian="body1">
              {EXIT_POPUP_SUBTITLE}
            </Typography>
            <div className={classes.buttonContainer}>
              <Button className={`${classes.popupButton} ${classes.popupButtonOutlined}`} onClick={handleClose}>
                {EXIT_POPUP_CLOSE_BUTTON}
              </Button>
              <Button className={classes.popupButton} onClick={handleCloseExit}>
                {EXIT_POPUP_EXIT_BUTTON}
              </Button>
            </div>
          </DialogContent>
        </div>
      </Dialog>
    </div>
  )
}
