import React from 'react'
import {
  ControlledAvatarField,
  ControlledCityIdField,
  ControlledTextField,
  ControlledDateField,
  ControlledSelectField,
  ControlledCourseField,
} from '../../FormElements'

import useStyles from './EditProfilePopup.styles'

function FirstTabProfile({ values, handlers }) {
  const {
    control,
    avatar,
    errors,
    genderValue,
    date,
    cohortValue,
    cohort,
    experienceValue,
    experience,
    degreeValue,
    education,
  } = values

  const {
    handleImageChange,
    handleChangeGender,
    handleBirthdayChange,
    handleChangeCohort,
    handleChangeDegree,
    handleChangeExperience,
  } = handlers

  const classes = useStyles()
  return (
    <fieldset className={classes.fieldset}>
      <div className={classes.fieldsGrid}>
        <ControlledAvatarField avatar={avatar} helperText={errors?.image?.message} onChange={handleImageChange} />
        <ControlledCityIdField
          control={control}
          error={!!errors?.city?.message}
          helperText={errors?.city?.message}
          width={306}
        />
        <ControlledTextField
          labelTitle="Имя"
          fieldName="first_name"
          control={control}
          error={!!errors.first_name?.message}
          helperText={errors.first_name?.message}
        />
        <ControlledTextField
          labelTitle="Фамилия"
          fieldName="last_name"
          control={control}
          error={!!errors.last_name?.message}
          helperText={errors.last_name?.message}
        />

        <ControlledSelectField
          labelTitle="Пол"
          tipContent="Выбери пол"
          value={genderValue}
          error={!!errors?.gender?.message}
          helperText={errors?.gender?.message}
          onChange={handleChangeGender}
          items={[
            { id: 'male', name: 'Мужской' },
            { id: 'female', name: 'Женский' },
          ]}
        />

        <ControlledDateField
          labelTitle="Дата рождения"
          fieldName="birth_date"
          dateValue={date}
          error={!!errors.birth_date?.message}
          helperText={errors.birth_date?.message}
          onChange={handleBirthdayChange}
        />
      </div>
      <div className={classes.fieldsGrid}>
        <ControlledCourseField
          control={control}
          error={!!errors?.course?.message}
          helperText={errors?.course?.message}
          width={306}
          name="course"
        />
        <ControlledSelectField
          labelTitle="Поток"
          tipContent="Поток"
          value={cohortValue}
          error={!!errors?.cohort_number?.message}
          helperText={errors?.cohort_number?.message}
          onChange={handleChangeCohort}
          items={cohort}
        />
        <ControlledSelectField
          labelTitle="Опыт работы"
          tipContent="Опыт работы"
          value={experienceValue}
          error={!!errors?.experience_type?.message}
          helperText={errors?.experience_type?.message}
          onChange={handleChangeExperience}
          items={experience}
          maxWidth={306}
        />
        <ControlledTextField
          labelTitle="Должность на предыдущем месте работы"
          fieldName="previous_job_position"
          control={control}
          error={!!errors.previous_job_position?.message}
          helperText={errors.previous_job_position?.message}
        />
        <ControlledSelectField
          labelTitle="Тип образования"
          tipContent="Тип образования"
          value={degreeValue}
          error={!!errors?.education_type}
          helperText={errors?.education_type?.message}
          onChange={handleChangeDegree}
          items={education}
          maxWidth={306}
        />
        <ControlledTextField
          labelTitle="Учебное заведение"
          fieldName="institution"
          control={control}
          error={!!errors.institution?.message}
          helperText={errors.institution?.message}
        />
      </div>
    </fieldset>
  )
}

export default React.memo(FirstTabProfile)
