import React, { useEffect, useMemo } from 'react'
import debounce from 'lodash.debounce'
import TextField from '@mui/material/TextField'
import InputAdornment from '@mui/material/InputAdornment'
import Autocomplete from '@mui/material/Autocomplete'
import CircularProgress from '@mui/material/CircularProgress'
import useStyles from './CourseSearchBar.style'
import api from '../../../../api/api'
import useAsync from '../../../../hooks/useAsync'
import { API_SEARCH_COURSES } from '../../../../utils/constants'
import IconSearch from '../../../../assets/Icons/IconSearch/IconSearch'

function CourseSearchBar({ onChange, value, error }) {
  const { run } = useAsync({
    data: [],
    error: null,
  })
  const [open, setOpen] = React.useState(false)
  const [inputValue, setInputValue] = React.useState(null)
  const [options, setOptions] = React.useState([])
  const [loading, setLoading] = React.useState(false)
  const classes = useStyles()

  const getCourses = async (query) => {
    try {
      setLoading(true)
      const response = await api.get(API_SEARCH_COURSES, { params: { search: query } })
      const cities = response?.data
      setOptions(cities ?? [])
      setOpen(cities?.length > 0)

      return Promise.resolve(response?.data)
    } catch (e) {
      return Promise.reject(e.response)
    } finally {
      setLoading(false)
    }
  }

  const changeHandler = (event) => {
    setInputValue(event?.target?.value)
  }

  const debouncedChangeHandler = useMemo(() => debounce(changeHandler, 300), [])

  useEffect(() => () => debouncedChangeHandler.cancel(), [])

  useEffect(() => {
    if (inputValue?.length > 2 && inputValue !== value?.name) {
      run(getCourses(inputValue))
    }
  }, [inputValue])

  useEffect(() => {
    if (!open) {
      setOptions([])
    }
  }, [open])

  return (
    <Autocomplete
      sx={{ width: 400, height: 40 }}
      clearText="Очистить"
      loadingText="Загрузка..."
      noOptionsText=""
      autoHighlight
      // freeSolo
      open={open}
      className={classes.root}
      classes={{ paper: classes.paper }}
      onOpen={() => {
        setOpen(options.length > 0)
      }}
      onClose={() => {
        setOpen(false)
      }}
      isOptionEqualToValue={(option) => option.id === value.id}
      getOptionLabel={(option) => option?.name}
      options={options}
      loading={loading}
      value={value}
      onInputChange={debouncedChangeHandler}
      onChange={(_, data) => {
        onChange(data)
      }}
      renderInput={(params) => (
        <TextField
          {...params}
          error={error}
          placeholder="Выбери курс"
          InputProps={{
            ...params.InputProps,
            startAdornment: (
              <InputAdornment position="start">
                <IconSearch />
              </InputAdornment>
            ),
            endAdornment: (
              <>
                {loading ? <CircularProgress color="inherit" size={20} /> : null}
                {params.InputProps.endAdornment}
              </>
            ),
          }}
        />
      )}
    />
  )
}

export default React.memo(CourseSearchBar)
