import React, { useEffect, useMemo, useState } from 'react'
import debounce from 'lodash.debounce'
import TextField from '@mui/material/TextField'
import InputAdornment from '@mui/material/InputAdornment'
import Autocomplete from '@mui/material/Autocomplete'
import CircularProgress from '@mui/material/CircularProgress'
import Tooltip from '@mui/material/Tooltip'
import useStyles from './ResponseSearchBar.style'
import api from '../../../../api/api'
import useAsync from "../../../../hooks/useAsync"
import { API_SEARCH_RESPONSES } from '../../../../utils/constants'
import IconSearch from "../../../../assets/Icons/IconSearch/IconSearch"

function ResponseSearchBar({ onChange, value, error, onResponseChange, responseValue }) {
  const [options, setOptions] = useState([])
  const { run } = useAsync({
    data: [],
    error: null,
  })
  const [open, setOpen] = useState(false)
  const [inputValue, setInputValue] = useState(null)

  const [loading, setLoading] = useState(false)
  const classes = useStyles()

  const getResponses = async (query) => {
    try {
      setLoading(true)
      const response = await api.get(API_SEARCH_RESPONSES, { params: { search: query } })
      const responses = response?.data
      // setOptions(responses && responses.map((c) => `${c?.company_name}`))
      setOptions(response?.data)
      setOpen(responses?.length > 0)

      return Promise.resolve(response?.data)
    } catch (e) {
      return Promise.reject(e.response)
    } finally {
      setLoading(false)
    }
  }

  const changeHandler = (event) => {
    if (event?.target?.value) {
      setInputValue(event?.target?.value)
      onChange(event?.target?.value)
    }
  }

  const debouncedChangeHandler = useMemo(() => debounce(changeHandler, 300), [])

  useEffect(() => () => debouncedChangeHandler.cancel(), [])

  useEffect(() => {
    if (inputValue?.length > 2 && inputValue !== value?.name) {
      run(getResponses(inputValue))
    }
  }, [inputValue])

  useEffect(() => {
    if (!open) {
      setOptions([])
    }
  }, [open])

  return (
    <Autocomplete
      sx={{ width: 400, height: 40 }}
      value={responseValue}
      key={responseValue}
      clearText="Очистить"
      loadingText="Загрузка..."
      noOptionsText=""
      autoHighlight
      disableClearable
      open={open}
      className={`${classes.root} ${classes.whiteBackground}`}
      classes={{ paper: classes.paper }}
      onOpen={() => {
        setOpen(options.length > 0)
      }}
      onClose={() => {
        setOpen(false)
      }}
      isOptionEqualToValue={(option) => option.company_name === value}
      getOptionLabel={(option) => option.company_name}
      filterOptions={(x) => x}
      options={options}
      loading={loading}
      onInputChange={debouncedChangeHandler}
      onChange={onResponseChange}
      renderInput={(params) => (
        <Tooltip
          title={params?.inputProps?.ref?.current?.value}
          classes={{ tooltip: classes.tooltip }}
          enterDelay={2500}
          leaveDelay={30}
        >
          <TextField
            {...params}
            error={error}
            placeholder="Введи название компании"
            InputProps={{
              ...params.InputProps,
              startAdornment: (
                <InputAdornment position="start">
                  <IconSearch />
                </InputAdornment>
              ),
              endAdornment: (
                <>
                  {loading ? <CircularProgress color="inherit" size={20} /> : null}
                  {params.InputProps.endAdornment}
                </>
              ),
            }}
          />
        </Tooltip>
      )}
    />
  )
}

export default React.memo(ResponseSearchBar)
