/* eslint-disable react/jsx-boolean-value */
/* eslint-disable no-unused-vars */
import React, { useEffect, useMemo } from 'react'
import debounce from 'lodash.debounce'
import TextField from '@mui/material/TextField'
import InputAdornment from '@mui/material/InputAdornment'
import Autocomplete from '@mui/material/Autocomplete'
import CircularProgress from '@mui/material/CircularProgress'
import useStyles from './SearchBar.style'
import api from '../../../../api/api'
import useAsync from '../../../../hooks/useAsync'
import { API_STUDENTS_LIST_ENDPOINT } from '../../../../utils/constants'
import IconSearch from '../../../../assets/Icons/IconSearch/IconSearch'

function SearchBar({ valueState, nameValue, width, endpoint }) {
  const { run } = useAsync({
    data: [],
    error: null,
  })
  const [open, setOpen] = React.useState(false)
  const [value, setValue] = valueState
  const [inputValue, setInputValue] = React.useState('')

  const [options, setOptions] = React.useState([])
  const [loading, setLoading] = React.useState(false)
  const classes = useStyles()

  const getStudentsList = async (query) => {
    try {
      setLoading(true)
      const response = await api.get(endpoint ?? API_STUDENTS_LIST_ENDPOINT, { params: { search: query } })
      const students = response?.data?.results
      setOptions(
        students &&
          students.map((s) => ({
            id: s?.id,
            name: `${s?.first_name} ${s?.last_name} (${s?.email})`,
            ...s,
          })),
      )
      setOpen(response?.data?.count > 0)
      return Promise.resolve(response?.data)
    } catch (e) {
      return Promise.reject(e.response)
    } finally {
      setLoading(false)
    }
  }

  const changeHandler = (event) => {
    if (event?.target) setInputValue(event.target?.value)
  }

  const debouncedChangeHandler = useMemo(() => debounce(changeHandler, 300), [])

  useEffect(() => () => debouncedChangeHandler.cancel(), [])

  useEffect(() => {
    if (inputValue?.length > 2 && inputValue !== value?.name) {
      run(getStudentsList(inputValue))
    }
  }, [inputValue])

  useEffect(() => {
    if (!open) {
      setOptions([])
    }
  }, [open])

  return (
    <Autocomplete
      sx={{ width: width || 400, height: 40 }}
      clearText="Очистить"
      loadingText="Загрузка..."
      noOptionsText=""
      open={open}
      className={classes.root}
      classes={{ paper: classes.paper }}
      onOpen={() => {
        setOpen(options.length > 0)
      }}
      onClose={() => {
        setOpen(false)
      }}
      isOptionEqualToValue={(option) => option.id === value.id}
      getOptionLabel={(option) => option.name || nameValue}
      filterOptions={(x) => x}
      options={options}
      loading={loading}
      value={value}
      onInputChange={debouncedChangeHandler}
      onChange={(event, newValue) => {
        setValue(newValue)
      }}
      renderInput={(params) => (
        <TextField
          {...params}
          placeholder="Поиск студента"
          InputProps={{
            ...params.InputProps,
            startAdornment: (
              <InputAdornment position="start">
                <IconSearch />
              </InputAdornment>
            ),
            endAdornment: (
              <>
                {loading ? <CircularProgress color="inherit" size={20} /> : null}
                {params.InputProps.endAdornment}
              </>
            ),
          }}
        />
      )}
    />
  )
}

export default React.memo(SearchBar)
