import React, { useState, useEffect, useMemo } from 'react'
import { useForm } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormHelperText,
  IconButton,
  Stack,
  ThemeProvider,
  Typography,
  Tooltip,
} from '@mui/material'
import KeyboardBackspaceIcon from '@mui/icons-material/KeyboardBackspace'
import isEqual from 'lodash.isequal'
import InputAdornment from '@mui/material/InputAdornment'
import useStyles from './NewContactPopup.style'
import { contactResponseOption, newContactSchema } from '../../../../utils/schemas'
import IconClose from '../../../../assets/Icons/IconClose/IconClose'
import useAsync from '../../../../hooks/useAsync'
import processServerNewContactError from '../../../../utils/server-newcontact-error'
import theme from '../../../../utils/themeMUI5'
import { ControlledSelectField, ControlledTextField } from '../../FormElements'
import TelegramIcon from '../../../../assets/telegram-icon.svg'
import IconDeleteButton from '../../../../assets/Icons/IconDeleteButton/IconDeleteButton'
import ControlledResponseField from '../../FormElements/controlled-response-field/controlled-response-field'
import api from '../../../../api/api'
import { API_CONTACTS_BY_ID_ENDPOINT, API_CONTACTS_ENDPOINT, ONLY_SPACES_REGEXP } from '../../../../utils/constants'
import { useAppDispatch } from '../../../../services/store/hooks'
import { enqueueSnackbar } from '../../../../services/slices/app-slice'

const NewContactPopup = ({
  openState,
  setSnack,
  onBackClick,
  cardData,
  contactsForNewResponseState,
  isNewReport,
  clearData,
  getValue,
  from = 'contacts',
  readOnly = false,
  data = undefined,
}) => {
  const dispatch = useAppDispatch()
  const classes = useStyles()
  const [open, setOpen] = openState
  // Стейт для списка откликов
  const [checkedResponses, setCheckedResponses] = useState([])
  // Стейт для инпута откликов
  const [responseValue, setResponseValue] = useState(undefined)
  // Стейт для списка контактов, когда отклик ещё не создан и контакты тоже
  const [contacts, setContacts] = contactsForNewResponseState || []
  // на будущее
  const socialNetworks = [
    {
      id: 1,
      name: 'Telegram',
      icon: TelegramIcon,
    },
  ]

  const [validationSchema, setValidationSchema] = useState(newContactSchema)
  const {
    control,
    handleSubmit,
    reset,
    setError,
    watch,
    setValue,
    clearErrors,
    register,
    trigger,
    formState: { errors },
  } = useForm({
    defaultValues: useMemo(
      () =>
        data?.id
          ? {
              ...data,
              contact_type: data?.social_networks?.length ? data?.social_networks[0]?.contact_type : undefined,
              contact_info: data?.social_networks?.length ? data?.social_networks[0]?.contact_info : undefined,
            }
          : { contact_type: 1 },
      [data, open],
    ),
    mode: 'onChange',
    resolver: useMemo(() => yupResolver(validationSchema), [validationSchema]),
  })

  const contactAdornment = {
    startAdornment: (
      <InputAdornment
        position="start"
        sx={{
          marginRight: 0,
          '.MuiTypography-root': {
            color: '#1a1b22',
            fontFamily: ['YS Text', '-apple-system', 'BlinkMacSystemFont', 'Arial', 'Helvetica', 'sans-serif'].join(
              ',',
            ),
            fontSize: 14,
            lineHeight: '20px',
          },
        }}
      >
        @
      </InputAdornment>
    ),
  }

  useEffect(() => {
    reset(
      data?.id
        ? {
            ...data,
            contact_type: data?.social_networks?.length ? data?.social_networks[0]?.contact_type : undefined,
            contact_info: data?.social_networks?.length ? data?.social_networks[0]?.contact_info : undefined,
          }
        : { contact_type: 1 },
    )
  }, [data, open])

  const { run } = useAsync({
    error: null,
  })

  const telephoneValue = watch('phone')
  const lastNameValue = watch('last_name')
  const responseFieldValue = watch('responses_field')
  const positionValue = watch('position')
  const contactInfoValue = watch('contact_info')
  const emailValue = watch('email')

  useEffect(async () => {
    await trigger('first_name')
  }, [telephoneValue, lastNameValue, responseFieldValue, positionValue, contactInfoValue, emailValue])

  useEffect(() => {
    clearErrors('responses_field')
    if (checkedResponses?.find((response) => response.id === responseValue?.id)) {
      setError('responses_field', {
        type: 'server',
        message: 'Нельзя добавлять один отклик дважды',
      })
    }
  }, [responseValue])

  useEffect(async () => {
    if (telephoneValue === '') {
      setValue('phone', null, { shouldValidate: true, shouldDirty: true })
    }
  }, [telephoneValue])

  useEffect(() => {
    if (positionValue === '') {
      setValue('position', undefined, { shouldValidate: true, shouldDirty: true })
    }
  }, [positionValue])

  useEffect(async () => {
    if (ONLY_SPACES_REGEXP.test(lastNameValue)) {
      setValue('last_name', null, { shouldValidate: true, shouldDirty: true })
    }
  }, [lastNameValue])

  const [showDeletePopup, setDeletePopup] = useState(false)

  const openDeletePopup = () => {
    setDeletePopup(true)
  }

  const closeDeletePopup = () => {
    setDeletePopup(false)
  }

  const handleClearSearchBar = () => {
    clearErrors('responses_field')
    setValue('responses_field', undefined, { shouldValidate: true, shouldDirty: true })
    setResponseValue(undefined)
  }

  const handleResetSearchBar = () => {
    handleClearSearchBar()
    setCheckedResponses([])
  }

  const handleClose = () => {
    setOpen(false)
    reset({ contact_type: 1 })
    clearErrors()
    handleResetSearchBar()
    if (data?.id) {
      clearData()
    }
  }

  const handleBackClick = () => {
    onBackClick()
    reset({ contact_type: 1 })
    clearErrors()
    handleResetSearchBar()
    if (data?.id) {
      clearData()
    }
  }

  const socialNetworkValue = watch('contact_type')

  const handleSocialNetworkChange = async (e) => {
    setValue('contact_type', e.target.value, { shouldValidate: true, shouldDirty: true })
  }

  useEffect(() => {
    register('contact_type')
  }, [register])

  const onSubmitContactCreation = async (contactData, message) => {
    if (isNewReport) {
      // +1, потому что есть проверки на data.id и 0 ловко их ломает. А первый элемент будет с id 0
      setContacts([...contacts, { ...contactData, id: contacts?.length + 1 }])
      handleBackClick()
      // Надо что-нибудь вернуть, вернём объект
      return contactData
    }
    try {
      const response = await api.post(API_CONTACTS_ENDPOINT, {
        ...contactData,
      })
      if (response.status === 200) {
        dispatch(
          enqueueSnackbar({
            info: {
              type: 'common',
              ...message,
            },
            options: {
              key: new Date().getTime() + Math.random(),
              autoHideDuration: 6000,
            },
          }),
        )
        // dispatch(
        //   enqueueSnackbar({
        //     options: {
        //       key: new Date().getTime() + Math.random(),
        //       autoHideDuration: 6000,
        //       content: (key) => <NotificationSnackbar id={key} {...message} />,
        //     },
        //   }),
        // )
        handleClose()
      }
      if (response.status === 201) {
        dispatch(
          enqueueSnackbar({
            info: {
              type: 'common',
              ...message,
            },
            options: {
              key: new Date().getTime() + Math.random(),
              autoHideDuration: 6000,
            },
          }),
        )
        // dispatch(
        //   enqueueSnackbar({
        //     options: {
        //       key: new Date().getTime() + Math.random(),
        //       autoHideDuration: 6000,
        //       content: (key) => <NotificationSnackbar id={key} {...message} />,
        //     },
        //   }),
        // )
        handleBackClick()
      }
      return Promise.resolve(response)
    } catch (e) {
      return processServerNewContactError(e, setError)
    }
  }

  const onSubmitContactPatch = async (contactData, message) => {
    if (isNewReport) {
      // 1. Находим в списке отгруженных, но не запушенный контактов
      // Сохраняем сам элемент и его индекс
      const changeIndex = contacts.findIndex((item) => item.id === data.id)
      const changedContact = { ...contacts[changeIndex] }
      // 2. Удаляем контакт из списка
      let changedContacts = [...contacts]
      changedContacts.splice(changeIndex, 1)
      // 3. Заносим в контакт новые данные
      // 3.1 нужно перебрать список, найти поля, которые не undefined и сохранить их
      Object.keys(contactData).forEach((field) => {
        if (contactData[field] !== undefined && !Array.isArray(contactData[field]))
          changedContact[field] = contactData[field]
      })
      // 4. Записываем новый контакт
      changedContacts = [...changedContacts, changedContact]
      setContacts(changedContacts)
      handleBackClick()
      // Надо что-нибудь вернуть, вернём объект
      return contactData
    }
    try {
      const response = await api.patch(API_CONTACTS_BY_ID_ENDPOINT(data?.id), {
        ...contactData,
      })
      if (response.status === 200) {
        dispatch(
          enqueueSnackbar({
            info: {
              type: 'common',
              ...message,
            },
            options: {
              key: new Date().getTime() + Math.random(),
              autoHideDuration: 6000,
            },
          }),
        )
        // dispatch(
        //   enqueueSnackbar({
        //     options: {
        //       key: new Date().getTime() + Math.random(),
        //       autoHideDuration: 6000,
        //       content: (key) => <NotificationSnackbar id={key} {...message} />,
        //     },
        //   }),
        // )
        handleBackClick()
      }
      return Promise.resolve(response)
    } catch (e) {
      return processServerNewContactError(e, setError)
    }
  }

  // Если уже есть добавленная компания, то надо отключить валидирование компании и наоборот
  useEffect(() => {
    // Если создаётся контакт к новой карточке, то опустим эту валидацию
    if (!isNewReport) {
      if (checkedResponses?.length) {
        const scheme = newContactSchema.omit(['responses_field'])
        setValidationSchema(scheme)
      } else {
        // Дополнительно удалим, вдруг ключ есть, и сработало лишний раз
        const scheme = newContactSchema.omit(['responses_field'])
        setValidationSchema(scheme.shape({ responses_field: contactResponseOption }))
      }
    }
  }, [checkedResponses])

  useEffect(() => {
    if (isNewReport) {
      const scheme = newContactSchema.omit(['responses_field'])
      setValidationSchema(scheme)
    } else {
      // Дополнительно удалим, вдруг ключ есть, и сработало лишний раз
      const scheme = newContactSchema.omit(['responses_field'])
      setValidationSchema(scheme.shape({ responses_field: contactResponseOption }))
    }
  }, [isNewReport])

  useEffect(() => {
    if (data?.id) {
      setCheckedResponses(data?.vacancy)
      setResponseValue(undefined)
      setValue('responses_field', undefined, { shouldValidate: true, shouldDirty: true })
    }
  }, [data, open])

  // Чтобы выставить отклик, когда создаём новый контакт в отклике
  useEffect(() => {
    if (cardData?.id && !data?.id) {
      setResponseValue({ company_name: cardData.company_name, id: cardData.id })
      setValue(
        'responses_field',
        { company_name: cardData.company_name, id: cardData.id },
        { shouldValidate: true, shouldDirty: true },
      )
    }
  }, [cardData, open])

  const submitHandler = (formData) => {
    let requestData = {}
    let message = {}
    let isDataUpdated = false

    if (checkedResponses.length === 5 && responseFieldValue) {
      setError('responses_field', {
        type: 'server',
        message: 'Один и тот же контакт возможно привязать максимум к 5 карточкам',
      })
      return
    }

    const formVacancyField = () => {
      let fieldData = []
      if (checkedResponses?.length) {
        fieldData = checkedResponses.map((response) => response?.id)
        // Массив для сверки изменились ли значения?
        if (responseValue?.id) fieldData = [...fieldData, responseValue?.id]
        return fieldData
      }
      return [responseValue?.id]
    }

    // Делаем массив айдишников, полученных с бэка, чтобы сравнить в массивом из формы
    const newVacancyIdArray = isNewReport ? undefined : data?.vacancy?.map((vacancy) => vacancy.id)

    const isFieldChanged = (fieldName) => {
      // TODO если будет несколько соцсетей, то с contact_info надо будет переделать
      if (fieldName === 'contact_info') {
        if (data.social_networks && data.social_networks[0]) {
          const isChanged =
            fieldName === 'contact_info'
              ? data && data.social_networks[0].contact_info !== formData.contact_info
              : //     : data && +data.social_networks[0][fieldName] !== +formData[fieldName]
                false
          if (isChanged) isDataUpdated = true
          return isChanged
        }
        if (formData?.contact_info && formData?.contact_info !== '') {
          isDataUpdated = true
          return true
        }
        return false
      }
      if (fieldName === 'vacancy' && data) {
        let isChanged = false
        // Собираем с помощью formVacancyField массив с данными о компаниями и сравниваем его
        if (!isEqual(newVacancyIdArray, formVacancyField())) isChanged = true
        if (isChanged) isDataUpdated = true
        return isChanged
      }
      const isChanged = data && data[fieldName] !== formData[fieldName]
      if (isChanged) isDataUpdated = true
      return isChanged
    }
    if (!data) {
      requestData = {
        first_name: formData.first_name,
        last_name: formData.last_name ?? undefined,
        position: formData.position,
        phone: formData.phone ?? undefined,
        email: formData.email,
        social_networks: formData.contact_info
          ? [
              {
                contact_type: formData.contact_type,
                contact_info: formData.contact_info,
              },
            ]
          : undefined,
        vacancy: formVacancyField(),
      }
      message = {
        open: true,
        title: 'Добавлен новый контакт',
        message: 'Данные успешно сохранены',
      }
    } else {
      requestData = {
        first_name: isFieldChanged('first_name') ? formData.first_name : undefined,
        last_name: isFieldChanged('last_name') ? formData.last_name : undefined,
        position: isFieldChanged('position') ? formData.position : undefined,
        phone: isFieldChanged('phone') ? formData.phone : undefined,
        email: isFieldChanged('email') ? formData.email : undefined,
        social_networks:
          // Если поле contact_info поменялось, то ставим contact_type = 1.
          // Если соцсетей станет больше, то переделаем
          // Если поменялось, но на пустое, то отправил null вместо массива
          // eslint-disable-next-line no-nested-ternary
          isFieldChanged('contact_info') && formData.contact_info === ''
            ? null
            : isFieldChanged('contact_info') && formData.contact_info !== ''
            ? [
                {
                  contact_type: 1,
                  // Если поле изменилось, но оно пустое, то отправим null
                  contact_info: formData.contact_info,
                  id: formData.social_networks[0] ? formData.social_networks[0].id : undefined,
                },
              ]
            : undefined,
        vacancy: isFieldChanged('vacancy') ? formVacancyField() : undefined,
      }
      message = {
        open: true,
        title: 'Контакт обновлён',
        message: 'Данные успешно сохранены',
      }
    }

    if (data && !isDataUpdated) {
      // eslint-disable-next-line no-unused-expressions
      isNewReport || data.id ? handleBackClick() : handleClose()
      dispatch(
        enqueueSnackbar({
          info: {
            type: 'common',
            title: 'Контакт не изменился',
            message: 'Вы не внесли изменения в свои данные',
          },
          options: {
            key: new Date().getTime() + Math.random(),
            autoHideDuration: 6000,
          },
        }),
      )
      return
    }

    // eslint-disable-next-line no-unused-expressions
    data?.id ? run(onSubmitContactPatch(requestData, message)) : run(onSubmitContactCreation(requestData, message))
  }

  // Обработчик для удаления выбранного отклика
  const handleDeleteCompany = () => {
    const deleteIndex = checkedResponses?.findIndex((response) => response.id === data?.id)
    const newState = [...checkedResponses]
    newState.splice(deleteIndex, 1)
    setCheckedResponses([...newState])
  }

  // Обработчик удаления созданного контакта
  const onDeleteContact = async () => {
    try {
      const response = await api.delete(API_CONTACTS_BY_ID_ENDPOINT(data?.id))
      if (response.status === 204) {
        dispatch(
          enqueueSnackbar({
            info: {
              type: 'common',
              title: 'Контакт удалён',
              message: 'Контакт успешно удалён',
            },
            options: {
              key: new Date().getTime() + Math.random(),
              autoHideDuration: 6000,
            },
          }),
        )
        if (data?.id || isNewReport) {
          closeDeletePopup()
          handleBackClick()
        } else {
          closeDeletePopup()
          handleClose()
        }
      }
      return Promise.resolve(response)
    } catch (e) {
      return processServerNewContactError(e, setError)
    }
  }

  // Обработчик для удаления несозданного контакта
  const onDeleteNewResponseContact = () => {
    // 1. Найти в списке контакт
    const deletedIndex = contacts?.find((contact) => contact?.id === data?.id)
    // 2. Удалить
    const newContactsState = [...contacts]
    newContactsState.splice(deletedIndex, 1)
    // 3. Запись изменённый список
    setContacts([...newContactsState])
    // 4. Закрываем все попапы
    closeDeletePopup()
    handleBackClick()
  }

  const handleDelete = () => {
    if (isNewReport) {
      onDeleteNewResponseContact()
    } else {
      run(onDeleteContact())
    }
  }

  const onResponseChange = (event, item) => {
    setResponseValue(item)
    setValue('responses_field', item || undefined, { shouldValidate: true, shouldDirty: true })
  }

  const handleAddCompany = () => {
    clearErrors('responses_field')
    if (checkedResponses.length >= 5) {
      setError('responses_field', {
        type: 'server',
        message: 'Один и тот же контакт возможно привязать максимум к 5 карточкам',
      })
      return
    }
    if (responseValue && !checkedResponses?.find((response) => response.id === responseValue.id)) {
      setCheckedResponses([...checkedResponses, responseValue])
      handleClearSearchBar()
    } else {
      setError('responses_field', {
        type: 'server',
        message: 'Нельзя добавлять один отклик дважды',
      })
    }
  }

  if (showDeletePopup) {
    return (
      <ThemeProvider theme={theme}>
        <Dialog
          variant="confirm"
          open={showDeletePopup}
          onClose={closeDeletePopup}
          sx={{
            '&.MuiDialog-paper': {
              width: 386,
              minWidth: 386,
              maxWidth: 386,
              height: 210,
              minHeight: 210,
              padding: 40,
              overflow: 'hidden',
            },
          }}
        >
          <DialogTitle>Удаление контакта</DialogTitle>

          <DialogContent sx={{ alignItems: 'start', marginTop: '5px' }}>Удалить этот контакт из списка?</DialogContent>

          <div className={classes.buttonContainer}>
            <Button
              color="secondary"
              sx={{ minHeight: '50px', maxHeight: '50px' }}
              type="submit"
              onClick={closeDeletePopup}
            >
              Оставить
            </Button>
            <Button color="primary" sx={{ minHeight: '50px', maxHeight: '50px' }} type="submit" onClick={handleDelete}>
              Удалить
            </Button>
          </div>
        </Dialog>
      </ThemeProvider>
    )
  }

  return (
    <ThemeProvider theme={theme}>
      <Dialog
        variant="response"
        open={open}
        onClose={handleClose}
        sx={{
          '& .MuiDialog-paper': {
            minWidth: 800,
            maxWidth: 996,
            height: 636,
            minHeight: 636,
            padding: 0,
            display: 'grid',
            gridTemplateColumns: '712px 284px',
            gridTemplateRows: '120px 1fr 112px',
          },
        }}
      >
        <DialogTitle
          variant="response"
          component="div"
          sx={{
            '&.MuiDialogTitle-root': {
              gridColumn: 1,
              gridRow: 1,
              padding: '40px 40px 20px',
              display: 'flex',
              justifyContent: 'space-between',
            },
          }}
        >
          <Button
            variant="text"
            onClick={handleBackClick}
            sx={{
              width: 'fit-content',
              outline: 'none!important',
              backgroundColor: 'transparent!important',
              color: '#000!important',
            }}
          >
            <KeyboardBackspaceIcon sx={{ mr: '12px' }} />
            {from === 'company' ? 'В карточку компании' : 'К общему списку контактов'}
          </Button>
          {data && !readOnly && (
            <Button variant="delete" sx={{ width: 144 }} onClick={openDeletePopup}>
              Удалить контакт
            </Button>
          )}
        </DialogTitle>
        <DialogContent
          variant="response"
          sx={{
            '&.MuiDialogContent-root': {
              gridColumn: 1,
              gridRow: 2,
              alignItems: 'inherit',
              padding: '13px 40px 20px',
            },
          }}
        >
          <form onSubmit={handleSubmit(submitHandler)} id="form">
            <Box className={classes.twoFieldInRowContainer}>
              <ControlledTextField
                labelTitle="Имя"
                placeholder="Введи имя"
                fieldName="first_name"
                control={control}
                error={!!errors.first_name?.message}
                helperText={errors.first_name?.message}
                disabled={readOnly}
              />
              <ControlledTextField
                labelTitle="Фамилия"
                placeholder="Введи фамилию"
                fieldName="last_name"
                control={control}
                error={!!errors.last_name?.message}
                helperText={errors.last_name?.message}
                disabled={readOnly}
              />
            </Box>
            <ControlledTextField
              labelTitle="Должность"
              placeholder="Введи название должности"
              fieldName="position"
              control={control}
              error={!!errors.position?.message}
              helperText={errors.position?.message}
              disabled={readOnly}
              sx={{
                width: '100%',
              }}
            />
            <Box className={classes.twoFieldInRowContainer}>
              <ControlledTextField
                labelTitle="Номер телефона"
                placeholder="Укажи номер телефона"
                fieldName="phone"
                control={control}
                error={!!errors.phone?.message}
                helperText={errors.phone?.message}
                disabled={readOnly}
              />
              <ControlledTextField
                labelTitle="Email"
                placeholder="Укажи email"
                fieldName="email"
                control={control}
                error={!!errors.email?.message}
                helperText={errors.email?.message}
                disabled={readOnly}
              />
            </Box>
            <Box sx={{ display: 'flex', flexDirection: 'row' }}>
              <ControlledSelectField
                labelTitle="Соц. сети"
                tipContent="Выбери соц. сеть"
                value={+socialNetworkValue}
                error={!!errors.contact_type?.message}
                helperText={errors.contact_type?.message}
                items={socialNetworks}
                onChange={handleSocialNetworkChange}
                maxWidth="158px"
                disabled
              />
              <ControlledTextField
                labelTitle=""
                placeholder="Введи ник"
                fieldName="contact_info"
                className={classes.contactInfo}
                control={control}
                error={!!errors.contact_info?.message}
                helperText={errors.contact_info?.message}
                disabled={readOnly}
                InputProps={contactAdornment}
                maxWidth="226px"
                sx={{
                  mt: '20px',
                  ml: '12px',
                }}
              />
            </Box>
          </form>
        </DialogContent>
        <DialogActions
          variant="response"
          sx={{
            '&.MuiDialogActions-root': {
              gridColumn: 1,
              gridRow: 3,
              padding: '0 40px 40px',
            },
          }}
        >
          <FormHelperText error style={{ textAlign: 'center', margin: '5px 0' }}>
            {errors?.non_field_errors?.message || ' '}
          </FormHelperText>
          {readOnly ? (
            <Button type="submit" sx={{ width: 285, minHeight: 50, maxHeight: 50 }} onClick={() => setOpen(false)}>
              Закрыть
            </Button>
          ) : (
            <Button form="form" type="submit" sx={{ width: 285, minHeight: 50, maxHeight: 50 }}>
              {data ? 'Сохранить контакт' : 'Добавить контакт'}
            </Button>
          )}
        </DialogActions>
        <Stack
          variant="response"
          spacing="6px"
          sx={{
            backgroundColor: '#f1f6ff',
            gridColumn: 2,
            gridRow: '1 / 4',
            padding: '40px 24px 40px 28px',
            boxSizing: 'border-box',
            position: 'relative',
          }}
        >
          <IconButton onClick={handleClose} variant="close-popup">
            <IconClose />
          </IconButton>
          {!isNewReport && (
            <>
              <Typography variant="body1" style={{ fontWeight: 500 }} mb="20px">
                Связать контакт с карточкой компании
              </Typography>
              <Box sx={{ display: 'flex', flexDirection: 'column', gap: '20px', margin: 0 }}>
                {checkedResponses?.map((response) => (
                  <Box key={response} sx={{ display: 'flex', alignItems: 'center', margin: 0 }}>
                    <Tooltip
                      title={response?.company_name}
                      classes={{ tooltip: classes.tooltip }}
                      enterDelay={500}
                      leaveDelay={200}
                    >
                      <Typography
                        sx={{
                          '&.MuiTypography-root': {
                            width: '204px',
                            backgroundColor: 'hsla(0, 0%, 100%, 1)',
                            height: '40px',
                            padding: '10px 12px',
                            border: '1px solid #DDE0E4',
                            borderRadius: '4px',
                          },
                        }}
                      >
                        {response?.company_name}
                      </Typography>
                    </Tooltip>
                    <IconButton sx={{ mb: '-1px' }} onClick={handleDeleteCompany}>
                      <IconDeleteButton />
                    </IconButton>
                  </Box>
                ))}
                <Box sx={{ display: 'flex', flexDirection: 'row', alignItems: 'center', margin: 0 }}>
                  <ControlledResponseField
                    control={control}
                    error={!!errors.responses_field?.message}
                    helperText={errors.responses_field?.message}
                    onResponseChange={onResponseChange}
                    responseValue={responseValue}
                    hideLabel
                    handleDelete={handleClearSearchBar}
                  />
                </Box>
              </Box>
              <Button
                variant="text"
                onClick={handleAddCompany}
                sx={{
                  width: 'fit-content',
                  outline: 'none!important',
                  backgroundColor: 'transparent!important',
                  color: '#488EFB',
                  margin: '0 !important',
                }}
              >
                + добавить ещё компанию
              </Button>
            </>
          )}
        </Stack>
      </Dialog>
    </ThemeProvider>
  )
}

export default NewContactPopup
