import React, { FC } from 'react'
import { Control, Controller } from 'react-hook-form'
import { FormHelperText, Typography } from '@mui/material/'
import CompanySearchBar from '../../Searchers/CompanySearchBar/CompanySearchBar'
import useStyles from '../styles'

type Props = {
  control: Control<any>
  error: boolean
  helperText: string
  width?: number | string
  hideLabel?: boolean
}

const ControlledCompanyField: FC<Props> = ({ control, error, helperText, width, hideLabel }) => {
  const classes = useStyles()

  return (
    <div className={classes.fieldContainer} style={{ width }}>
      {!hideLabel && <Typography variant="body2">Компания</Typography>}
      <Controller
        name="company_name"
        control={control}
        render={({ field }) => <CompanySearchBar value={field?.value} onChange={field?.onChange} error={error} />}
      />
      <div className={classes.input} style={{ marginTop: -4 }}>
        <FormHelperText className="Mui-error">{helperText || ' '}</FormHelperText>
      </div>
    </div>
  )
}

export default React.memo(ControlledCompanyField)
