import React from 'react'
import { Box, Grid, Typography } from '@material-ui/core'
import { useDrop } from 'react-dnd'
import useStyles from './ColumnGrid.styles'
import ToolButton from '../../../../Sub/Buttons/ToolButton/ToolButton'
import CompanyCard from '../CompanyCard/CompanyCard'
import declOfNum from '../../../../../utils/declOfNum'
import { useAppDispatch } from '../../../../../services/store/hooks'
import { updateResponseStatus } from '../../../../../services/slices/diary-slice'

const ColumnGrid = ({
  openResponsePopup,
  cards,
  status,
  handleToggleEditPopup,
  handleSetCardData,
  handleOpenPreviewPopup,
  readOnly,
  ...props
}) => {
  const dispatch = useAppDispatch()
  const { title } = status
  const addResponse = status.status_id === 'response'
  const classes = useStyles()
  const onDropHandler = (item) => {
    // eslint-disable-next-line no-useless-return
    if (item.status_id === status.status_id) return
    // dispatch(updateResponseStatus({ ...item, newStatus: status.status_id }))
  }

  const [{ isActive }, dropTarget] = useDrop({
    accept: 'card',
    drop(itemId) {
      onDropHandler(itemId)
    },

    collect: (monitor) => ({
      isActive: monitor.canDrop() && monitor.isOver() && monitor.getItem()?.status_id !== status.status_id,
    }),
  })

  return (
    <Grid {...props} className={classes.root} ref={dropTarget}>
      <Box className={classes.columnHead}>
        <Typography className={classes.columnHeader} variant="h2">
          {title}
        </Typography>
        <Typography className={classes.columnSubheader} variant="subtitle1">
          {cards.length} {declOfNum(cards.length, ['вакансия', 'вакансии', 'вакансий'])}
        </Typography>
      </Box>
      <Box className={classes.cardsContainer}>
        {addResponse && !readOnly && (
          <ToolButton onClick={openResponsePopup} typeSecondary typeBig>
            <Box className={classes.buttonBox}>
              <Typography className={classes.buttonPlus}>+</Typography>
              <Typography className={classes.buttonContent}>Добавить новый отклик</Typography>
            </Box>
          </ToolButton>
        )}
        {cards.map((card) => (
          <CompanyCard
            handleOpenPopup={readOnly ? handleOpenPreviewPopup : handleToggleEditPopup}
            handleSetCardData={handleSetCardData}
            key={card.id}
            card={card}
          />
        ))}
        {isActive && <CompanyCard />}
      </Box>
    </Grid>
  )
}

export default ColumnGrid
