import React, { FC } from 'react'
import { Controller, Control } from 'react-hook-form'
import { Typography, TextField, InputProps } from '@mui/material/'
import useStyles from '../styles'

type TProps = {
  labelTitle?: string
  fieldName: string
  control: Control<any>
  error?: boolean
  helperText?: string
  textClass?: string
  inputProps?: InputProps
  maxWidth?: string | number
  maxSymbol?: number
  symbolsCount?: number
  minLength?: string | number
  maxLength?: string | number
  width?: string | number
  height?: string | number
  placeholder?: string
  multiline?: boolean
  rows?: number
  marginLeft?: string | number
  marginRight?: string | number
  marginTop?: string | number
  marginBottom?: string | number
  margin?: string
  disabled?: boolean
}

const ControlledTextField: FC<TProps> = ({
  labelTitle,
  fieldName,
  control,
  error,
  helperText,
  textClass = undefined,
  inputProps = undefined,
  maxWidth,
  maxSymbol = undefined,
  symbolsCount = undefined,
  minLength = undefined,
  maxLength = undefined,
  width = undefined,
  height = undefined,
  marginLeft,
  marginRight,
  marginTop,
  marginBottom,
  margin,
  ...props
}) => {
  const classes = useStyles()
  const textClasses = textClass ? `${classes.input} ${textClass}` : classes.input
  return (
    <div className={labelTitle ? classes.fieldContainer : undefined} style={{ maxWidth, minWidth: maxWidth }}>
      {labelTitle && <Typography variant="body2">{labelTitle}</Typography>}

      <Controller
        name={fieldName}
        control={control}
        render={({ field }) => (
          <TextField
            {...field}
            className={textClasses}
            variant="outlined"
            InputProps={inputProps || {}}
            error={error}
            helperText={helperText || ' '}
            sx={{
              maxWidth,
              minWidth: width,
              height,
              marginLeft,
              marginRight,
              marginTop,
              marginBottom,
              margin,
            }}
            {...props}
          />
        )}
      />
      {minLength && (
        <Typography className={classes.textCounter}>
          {symbolsCount}/{minLength}-{maxLength}
        </Typography>
      )}
      {!minLength && maxLength && (
        <Typography className={classes.textCounter}>
          {symbolsCount}/{maxLength}
        </Typography>
      )}
    </div>
  )
}

export default React.memo(ControlledTextField)
