/* eslint-disable no-param-reassign */
import React, { useState, useEffect } from 'react'

import useStyles from './DiaryPage.style'
import api from '../../../api/api'
import ToolPanel from '../../../components/Sub/ToolPanel/ToolPanel'
import Diary from '../../../components/Main/DiaryPage/Diary/Diary'
import { API_CONTACTS_SEVERAL_ENDPOINT } from '../../../utils/constants'
import LoadingOverlay from '../../../components/Sub/DataTable/LoadingOverlay/LoadingOverlay'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import {
  createResponse,
  deleteResponse,
  getCurrencies,
  getResponses,
  patchResponse,
} from '../../../services/axios/diary'

const DiaryPage = ({ setSnack }) => {
  const { responses: cards, currencies, status } = useAppSelector((state) => state.diary)
  const dispatch = useAppDispatch()
  const cardDataState = useState(null)
  const openState = useState(false)
  const [, setOpen] = openState
  const [cardData] = cardDataState
  const [contactsForNewResponse, setContactsForNewResponse] = useState([])
  const classes = useStyles()

  const isLoading = status === 'pending'

  const onSubmitVacancy = async (dataForm) => {
    try {
      const result = await dispatch(createResponse(dataForm)).unwrap()
      // Если есть контакты, то надо вторым запросом отправить их
      // Используя id отклика
      // + надо удалить из объектов массива поле id, которое добавил чтобы нормально рендерилась таблица
      const contactsArray = contactsForNewResponse.map((contact) => {
        // Удаляем id контакта, который сделали просто чтобы отрисовать таблицу
        // И добавляем в контакт поле вакансии

        const newContact = { ...contact, vacancy: [result?.id] }
        delete newContact.id
        return newContact
      })
      if (contactsArray?.length) await api.post(API_CONTACTS_SEVERAL_ENDPOINT, contactsArray)
      setContactsForNewResponse([])
      setOpen(false)
      return Promise.resolve([...cards, result])
    } catch (e) {
      // Очистка поля контактов если всё сломалось
      setContactsForNewResponse([])
      return Promise.reject(e)
    }
  }

  const onPatchVacancy = async (dataForm) => {
    try {
      const result = await dispatch(patchResponse({ data: dataForm, id: cardData.id })).unwrap()
      setOpen(false)
      return Promise.resolve([...cards, result])
    } catch (e) {
      return Promise.reject(e)
    }
  }
  const onDeleteCard = async () => {
    try {
      const result = await dispatch(deleteResponse(cardData.id)).unwrap()
      setOpen(false)
      return Promise.resolve([...cards, result])
    } catch (e) {
      return Promise.reject(e?.response)
    }
  }
  useEffect(() => dispatch(getCurrencies()), [])
  useEffect(() => dispatch(getResponses()), [])

  return (
    <div className={classes.root}>
      <ToolPanel setSnack={setSnack} />
      {isLoading && <LoadingOverlay />}
      <Diary
        cards={cards}
        states={{ openState, cardDataState }}
        handlers={{ onPatchVacancy, onDeleteCard, onSubmitVacancy, setSnack }}
        currencies={currencies}
        contactsForNewResponseState={[contactsForNewResponse, setContactsForNewResponse]}
      />
    </div>
  )
}
export default DiaryPage
